import React, { useState, useEffect } from 'react'
import { useRecoilState } from 'recoil'
import { BigNumber } from 'bignumber.js'

import BoxHeader from './BoxHeader.js'

import {
    addToastMsg,
    withdraw_liquidity,
    numFormat,
    float_to_decimal_int,
    checkAndUpdateComponentTransactions,
    isPoolUpToDate
} from '../services/amm'
import { withdrawLiquiditySlippage, transactionsStatusSelector } from '../atoms'

import { GAbutton } from '../services/ga.js'

import {
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    Card,
    Box,
    Grid,
    CardHeader,
    Typography,
    Slider,
    Stack,
    InputAdornment,
    Paper
} from '@mui/material'
import MuiInput from '@mui/material/Input'
import { styled } from '@mui/material/styles'
import { StyledLoadingButton, StyledButtonTypography } from './StyleCustomizations'
import useIsMobile from '../hooks/useIsMobile.js'

const Item = styled(Paper)(({ theme }) => ({
    ...theme.typography.body2,
    padding: theme.spacing(0),
    textAlign: 'left',
    color: theme.palette.text.secondary
}))

const Input = styled(MuiInput)`
    width: 42px;
`

export default function RemoveLiquidity(props) {
    const [open, setOpen] = useState(true)
    const [percent, setPercent] = useState(25)
    const [minToken1Amount, setMinToken1Amount] = useState('') // fixme: what happens if token1amount / pool share changes during op
    const [minToken2Amount, setMinToken2Amount] = useState('')
    const [removeLiquidityInProgress, setRemoveLiquidityInProgress] = useState(false)
    const [transactionsStatus] = useRecoilState(
        transactionsStatusSelector('withdraw_liquidity')
    )
    const [componentTxs, setComponentTxs] = useState({})
    const [slippage] = useRecoilState(withdrawLiquiditySlippage)

    const isMobile = useIsMobile()

    useEffect(() => {
        checkAndUpdateComponentTransactions(
            transactionsStatus,
            componentTxs,
            setRemoveLiquidityInProgress
        )
    }, [transactionsStatus, componentTxs])

    const marks = [
        { value: 0, label: '0%' },
        { value: 25, label: '25%' },
        { value: 50, label: '50%' },
        { value: 75, label: '75%' },
        { value: 100, label: '100%' }
    ]

    useEffect(() => {
        // if one of the tokens is '0' then set the other one to zero as well
        if (BigNumber(minToken1Amount).isZero()) setMinToken2Amount('0')
        if (BigNumber(minToken2Amount).isZero()) setMinToken1Amount('0')

        // fixme: dup logic from getMinTokenAmount
        setMinToken1Amount(
            BigNumber(props.token1Amount)
                .times(percent)
                .shiftedBy(-2)
                .times(BigNumber(1).minus(BigNumber(slippage).shiftedBy(-2)))
                .toString(10, BigNumber.ROUND_DOWN)
        )
        setMinToken2Amount(
            BigNumber(props.token2Amount)
                .times(percent)
                .shiftedBy(-2)
                .times(BigNumber(1).minus(BigNumber(slippage).shiftedBy(-2)))
                .toString(10, BigNumber.ROUND_DOWN)
        )

        setRemoveLiquidityInProgress(false)
    }, [
        open,
        minToken1Amount,
        minToken2Amount,
        percent,
        props.token1Amount,
        props.token2Amount,
        slippage
    ])

    const handleClose = () => {
        setRemoveLiquidityInProgress(false)
        setOpen(false)
        props.openRemoveLiquidity(false)
    }

    const setAmounts = (percent) => {
        setMinToken1Amount(getMinTokenAmount(props.token1Amount, percent))
        setMinToken2Amount(getMinTokenAmount(props.token2Amount, percent))
    }

    const updateState = (newPercent) => {
        setPercent(newPercent)
        setAmounts(newPercent)
        console.log(
            'Remove Liquidity, updateState: ',
            minToken1Amount,
            minToken2Amount,
            props.token1Amount,
            props.token2Amount
        )
    }

    const handleSliderChange = (event, newPercent) => {
        updateState(newPercent)
    }

    const handleInputChange = (event) => {
        let newPercent = event.target.value === '' ? '' : Number(event.target.value)
        updateState(newPercent)
        console.log('handle input change')
    }

    const handleBlur = () => {
        if (percent < 0) {
            setPercent(0)
        } else if (percent > 100) {
            setPercent(100)
        }
    }

    const getMinTokenAmount = (tokenAmount, percent) => {
        let minAmount = BigNumber(tokenAmount)
            .times(percent)
            .shiftedBy(-2)
            .times(BigNumber(1).minus(BigNumber(slippage).shiftedBy(-2)))
            .toString(10, BigNumber.ROUND_DOWN)
        return minAmount
    }

    const handleWithdraw = async () => {
        if (!isPoolUpToDate(props.poolId)) {
            GAbutton('withdraw-not-up-to-date')
            return
        }

        console.log('Withdraw percent: ', percent)
        let sharesToWithdraw = BigNumber(props.balance)
            .times(BigNumber(percent))
            .shiftedBy(-2)
            .toFixed(0, BigNumber.ROUND_DOWN)
        let tmpMinToken1Amount = minToken1Amount
        let tmpMinToken2Amount = minToken2Amount

        console.log(
            'handleWithdraw: ',
            sharesToWithdraw,
            minToken1Amount,
            minToken2Amount
        )

        // Make sure we send amounts in the right order
        if (props.token1 > props.token2) {
            let tmp = tmpMinToken1Amount
            tmpMinToken1Amount = tmpMinToken2Amount
            tmpMinToken2Amount = tmp
        }

        setRemoveLiquidityInProgress(true)

        try {
            let label =
                'withdraw:' +
                props.poolId +
                ':' +
                tmpMinToken1Amount +
                ':' +
                tmpMinToken2Amount

            let tx = await withdraw_liquidity(
                props.poolId,
                sharesToWithdraw,
                float_to_decimal_int(props.token1, tmpMinToken1Amount),
                float_to_decimal_int(props.token2, tmpMinToken2Amount),
                label
            )

            if (!tx) throw Error('Cannot perform transaction')

            setComponentTxs((prevState) => ({
                ...prevState,
                [tx.transaction_hash]: 'new'
            }))
        } catch (e) {
            if (e.message.includes('User abort')) {
                addToastMsg('Remove Liquidity: User Aborted', 'error')
            }
            setRemoveLiquidityInProgress(false)
        }
        handleClose()
    }

    return (
        <div>
            <Dialog
                open={open}
                scroll='body'
                onClose={handleClose}
                sx={{ height: '100%', width: '100%' }}
                PaperProps={{
                    elevation: 0,
                    style: {
                        backgroundColor: 'transparent',
                        maxWidth: isMobile ? 'calc(100% - 16px)' : 'calc(100% - 32px)',
                        width: isMobile ? 'calc(100% - 16px)' : 'auto',
                        margin: isMobile ? '8px' : '16px'
                    }
                }}>
                <Box
                    sx={{
                        p: isMobile ? 2 : 3,
                        border: '1px solid',
                        borderColor: 'rgba(255, 255, 255, 0.27)',
                        boxShadow: 40,
                        borderRadius: 2,
                        overflow: 'hidden',
                        backgroundColor: '#0d0c1e'
                    }}>
                    <Grid container sx={{ mb: 2 }}>
                        <BoxHeader name='Remove Liquidity' />
                    </Grid>

                    <DialogContent sx={{ p: 0, mb: 0 }}>
                        <Card
                            sx={{
                                p: isMobile ? 1 : 2,
                                boxShadow: 2,
                                mb: 1,
                                backgroundColor: '#201e43'
                            }}>
                            <DialogContentText
                                sx={{
                                    fontSize: isMobile ? 13 : 18,
                                    fontWeight: 'bold',
                                    ml: 2
                                }}>
                                Percent of liquidity shares to remove
                            </DialogContentText>

                            <Box sx={{ width: isMobile ? '100%' : 500, mt: 3, ml: 2 }}>
                                <Grid
                                    container
                                    spacing={isMobile ? 0 : 2}
                                    alignItems='center'>
                                    <Grid
                                        item
                                        xs
                                        sx={{ mt: 2, ml: isMobile ? 0 : 2, mr: 2 }}>
                                        <Slider
                                            aria-label='Custom marks'
                                            defaultValue={25}
                                            valueLabelDisplay='auto'
                                            marks={marks}
                                            value={
                                                typeof percent === 'number' ? percent : 0
                                            }
                                            onChange={handleSliderChange}
                                            aria-labelledby='input-slider'
                                        />
                                    </Grid>
                                    <Grid item sx={{ mx: 2 }}>
                                        <Box
                                            sx={{
                                                p: 1,
                                                pr: 4,
                                                borderColor: 'rgba(255, 255, 255, 0.27)',
                                                boxShadow: 40,
                                                borderRadius: 1,
                                                backgroundColor: '#0d0c1e'
                                            }}>
                                            <Input
                                                value={percent}
                                                size='small'
                                                onChange={handleInputChange}
                                                onBlur={handleBlur}
                                                sx={{ ml: 1 }}
                                                style={{ fontSize: isMobile ? 12 : 20 }}
                                                endAdornment={
                                                    <InputAdornment
                                                        position='end'
                                                        sx={{
                                                            mr: -3,
                                                            mb: 0.25,
                                                            color: 'rgba(200, 200, 200, 0.5)'
                                                        }}>
                                                        %
                                                    </InputAdornment>
                                                }
                                                inputProps={{
                                                    step: 1,
                                                    min: 0,
                                                    max: 100,
                                                    type: 'number',
                                                    'aria-labelledby': 'input-slider'
                                                }}
                                            />
                                        </Box>
                                    </Grid>
                                </Grid>
                            </Box>
                        </Card>
                        <Card
                            sx={{
                                p: isMobile ? 1 : 2,
                                boxShadow: 2,
                                mb: 0,
                                backgroundColor: '#201e43'
                            }}>
                            <CardHeader
                                title={
                                    <Grid container direction='row' alignItems='center'>
                                        <Typography
                                            sx={{
                                                fontSize: isMobile ? 14 : 18,
                                                fontWeight: 'bold'
                                            }}>
                                            {isMobile ? 'Min' : 'Minimum amount'} received
                                        </Typography>
                                        <Typography
                                            sx={{
                                                fontSize: isMobile ? 12 : 14,
                                                ml: 1,
                                                color: 'rgba(240, 240, 240, 0.7)'
                                            }}>
                                            (slippage={slippage}%)
                                        </Typography>
                                    </Grid>
                                }
                                disableTypography={true}
                                sx={{
                                    fontSize: isMobile ? 14 : 18,
                                    px: isMobile ? 1 : 2,
                                    fontWeight: 'bold',
                                    mt: -2
                                }}
                            />
                            <Grid
                                item
                                container
                                alignItems='center'
                                spacing={0}
                                sx={{ px: isMobile ? 0 : 2, ml: 0 }}>
                                <Grid item xs={12}>
                                    <Stack
                                        direction='row'
                                        spacing={0}
                                        alignItems='center'
                                        justifyContent='space-between'>
                                        <Stack
                                            direction='row'
                                            spacing={0}
                                            justifyContent='flex-start'
                                            alignItems='center'>
                                            <Item
                                                sx={{ boxShadow: 0, mr: 1 }}
                                                style={{
                                                    backgroundColor: 'transparent'
                                                }}>
                                                <img
                                                    src={props.token1Icon}
                                                    alt=''
                                                    width={isMobile ? 32 : 48}
                                                    height={isMobile ? 32 : 48}
                                                />
                                            </Item>
                                            <Item
                                                sx={{ boxShadow: 0 }}
                                                style={{
                                                    backgroundColor: 'transparent'
                                                }}>
                                                <Typography
                                                    sx={{
                                                        fontSize: isMobile ? 14 : 20,
                                                        fontWeight: 'bold',
                                                        mb: 1
                                                    }}>
                                                    {props.token1Symbol}:
                                                </Typography>
                                            </Item>
                                        </Stack>
                                        <Item
                                            sx={{ boxShadow: 0, mb: 1 }}
                                            style={{ backgroundColor: 'transparent' }}>
                                            <Typography
                                                sx={{ fontSize: isMobile ? 14 : 20 }}>
                                                {numFormat(minToken1Amount)}
                                            </Typography>
                                        </Item>
                                    </Stack>

                                    <Stack
                                        direction='row'
                                        spacing={0}
                                        alignItems='center'
                                        justifyContent='space-between'>
                                        <Stack
                                            direction='row'
                                            spacing={0}
                                            justifyContent='flex-start'
                                            alignItems='center'>
                                            <Item
                                                sx={{ boxShadow: 0, mr: 1 }}
                                                style={{
                                                    backgroundColor: 'transparent'
                                                }}>
                                                <img
                                                    src={props.token2Icon}
                                                    alt=''
                                                    width={isMobile ? 32 : 48}
                                                    height={isMobile ? 32 : 48}
                                                />
                                            </Item>
                                            <Item
                                                sx={{ boxShadow: 0 }}
                                                style={{
                                                    backgroundColor: 'transparent'
                                                }}>
                                                <Typography
                                                    sx={{
                                                        fontSize: isMobile ? 14 : 20,
                                                        fontWeight: 'bold',
                                                        mb: 1
                                                    }}>
                                                    {props.token2Symbol}:
                                                </Typography>
                                            </Item>
                                        </Stack>
                                        <Item
                                            sx={{ boxShadow: 0, mb: 1 }}
                                            style={{ backgroundColor: 'transparent' }}>
                                            <Typography
                                                sx={{ fontSize: isMobile ? 14 : 20 }}>
                                                {numFormat(minToken2Amount)}
                                            </Typography>
                                        </Item>
                                    </Stack>
                                </Grid>
                            </Grid>
                        </Card>
                    </DialogContent>
                    <DialogActions sx={{ justifyContent: 'center', mt: 1, mb: -2 }}>
                        <StyledLoadingButton
                            fullWidth={true}
                            disabled={
                                minToken1Amount === '0' ||
                                minToken2Amount === '0' ||
                                removeLiquidityInProgress
                                    ? true
                                    : false
                            }
                            loading={removeLiquidityInProgress === true}
                            loadingPosition='start'
                            sx={{ width: '50%', justifyContent: 'center' }}
                            onClick={handleWithdraw}>
                            <StyledButtonTypography sx={{ fontSize: isMobile ? 12 : 20 }}>
                                {removeLiquidityInProgress ? 'REMOVING' : 'REMOVE'}
                            </StyledButtonTypography>
                        </StyledLoadingButton>
                    </DialogActions>
                </Box>
            </Dialog>
        </div>
    )
}
