import React from 'react'
import { Typography, Grid, Link, Box } from '@mui/material'
import { getRecoil } from 'recoil-nexus'
import { tokensState } from '../atoms'
import { decimal_int_to_float, numFormat } from '../services/amm'

function PendingTransaction(props) {
    const handleClick = () => {
        window.open('https://goerli.voyager.online/tx/' + props.tx.hash, '_blank')
    }

    const transactionInfo = () => {
        let tx = props.tx
        let tokens = getRecoil(tokensState)
        let symbol1 = tokens.get(tx.token1).symbol

        if (props.tx.type.includes('approve')) {
            return 'Approve ' + symbol1
        }

        let symbol2 = tokens.get(tx.token2).symbol
        let token1Amount = numFormat(decimal_int_to_float(tx.token1, tx.token1Amount))
        let token2Amount = numFormat(decimal_int_to_float(tx.token2, tx.token2Amount))

        if (props.tx.type.includes('swap')) {
            return (
                'Swap ' +
                token1Amount +
                ' ' +
                symbol1 +
                ' for ' +
                token2Amount +
                ' ' +
                symbol2
            )
        }

        if (props.tx.type.includes('add_liquidity')) {
            return (
                'Add Liquidity ' +
                token1Amount +
                ' ' +
                symbol1 +
                ' and ' +
                token2Amount +
                ' ' +
                symbol2 +
                ' '
            )
        }

        if (props.tx.type.includes('withdraw_liquidity')) {
            return (
                'Withdraw Liquidity ' +
                token1Amount +
                ' ' +
                symbol1 +
                ' and ' +
                token2Amount +
                ' ' +
                symbol2 +
                ' '
            )
        }
    }

    return (
        <Box width='100%' borderRadius={0.7} sx={{ backgroundColor: '#201e43', mt: 2 }}>
            <Grid
                container
                direction='row'
                padding={1}
                width='100%'
                alignItems='center'
                justifyContent='space-between'>
                <Link onClick={handleClick} underline='hover' sx={{ mr: 4 }}>
                    <Typography fontSize={16} color='#d0d0d0'>
                        {transactionInfo()}
                    </Typography>
                </Link>
                <Typography fontSize={16} color='#d0d0d0'>
                    {props.tx.status.toUpperCase()}
                </Typography>
            </Grid>
        </Box>
    )
}

export default PendingTransaction
