import React, { useState } from 'react'
import { useRecoilState } from 'recoil'
import { BigNumber } from 'bignumber.js'
import {
    Card,
    Grid,
    CardActions,
    CardContent,
    Typography,
    Stack,
    Paper,
    Box,
    Divider,
    styled
} from '@mui/material'

import { tokensState, poolsState } from '../atoms'
import { decimal_int_to_float, numFormat } from '../services/amm'
import RemoveLiquidity from './RemoveLiquidity'
import { StyledLoadingButton, StyledButtonTypography } from './StyleCustomizations'
import useIsMobile from '../hooks/useIsMobile'

const Item = styled(Paper)(({ theme }) => ({
    ...theme.typography.body2,
    padding: theme.spacing(0),
    textAlign: 'left',
    color: theme.palette.text.secondary
}))

function LiquidityPool(props) {
    const [tokens] = useRecoilState(tokensState)
    const [pools] = useRecoilState(poolsState)
    const [openRemoveLiquidity, setOpenRemoveLiquidity] = useState(false)

    const isMobile = useIsMobile()

    BigNumber.set({ DECIMAL_PLACES: 40 })

    let pool = pools.get(props.poolId)
    if (!pool || !pool.shares) return <div></div>

    const token1 = () => {
        let pool = pools.get(props.poolId)
        let token_address = pool.token1
        let token = tokens.get(token_address)
        return token
    }

    const token1Symbol = () => {
        let token = token1(props.poolId)
        return token.symbol
    }

    const token1Icon = () => {
        let token = token1(props.poolId)
        return token.icon
    }

    const userPoolShare = () => {
        let pool = pools.get(props.poolId)
        if (!pool.shares) return '0'
        return BigNumber(props.balance).div(BigNumber(pool.shares))
    }

    const userToken1AmountDecimal = () => {
        let pool = pools.get(props.poolId)
        let user_share = userPoolShare()
        return BigNumber(user_share).times(pool.token1Amount)
    }

    const userToken1Amount = () => {
        return decimal_int_to_float(pool.token1, userToken1AmountDecimal())
    }

    const token2 = () => {
        let pool = pools.get(props.poolId)
        let token_address = pool.token2
        let token = tokens.get(token_address)
        return token
    }

    const token2Symbol = () => {
        let token = token2(props.poolId)
        return token.symbol
    }

    const token2Icon = () => {
        let token = token2(props.poolId)
        return token.icon
    }

    const userToken2AmountDecimal = () => {
        let pool = pools.get(props.poolId)
        let user_share = userPoolShare()
        return BigNumber(user_share).times(pool.token2Amount)
    }

    const userToken2Amount = () => {
        return decimal_int_to_float(pool.token2, userToken2AmountDecimal())
    }

    const userBalance = () => {
        let d1 = token1().decimals
        let d2 = token2().decimals
        let d_balance = Math.floor((d1 + d2) / 2)
        return BigNumber(props.balance).shiftedBy(-d_balance).toString(10)
    }

    const handleClickWithdraw = () => {
        setOpenRemoveLiquidity(true)
    }

    const handleAddMore = () => {
        props.addMoreCallback(props.poolId)
    }

    const renderRemoveLiquidity = () => {
        if (openRemoveLiquidity) {
            return (
                <RemoveLiquidity
                    poolId={props.poolId}
                    balance={props.balance}
                    token1Symbol={token1Symbol()}
                    token2Symbol={token2Symbol()}
                    token1Icon={token1Icon()}
                    token2Icon={token2Icon()}
                    token1={token1().address}
                    token2={token2().address}
                    token1Amount={userToken1Amount()}
                    token2Amount={userToken2Amount()}
                    openRemoveLiquidity={setOpenRemoveLiquidity}
                />
            )
        }
    }

    return (
        <Box
            sx={{
                p: isMobile ? 1.5 : 3,
                border: isMobile ? 'none' : '1px solid',
                borderColor: 'rgba(255, 255, 255, 0.27)',
                mt: 2,
                mb: 0,
                boxShadow: 40,
                borderRadius: isMobile ? 1.5 : 2,
                backgroundColor: '#0d0c1e'
            }}>
            <Card
                sx={{
                    py: 3,
                    px: isMobile ? 1 : 3,
                    boxShadow: 2,
                    mb: 1,
                    backgroundColor: '#201e43'
                }}>
                <Typography
                    sx={{
                        fontSize: isMobile ? 13 : 18,
                        fontWeight: '500',
                        ml: 2,
                        mb: 0.5,
                        mt: -1
                    }}>
                    Your Position
                </Typography>

                <Divider
                    variant='middle'
                    sx={{
                        borderBottomWidth: 1,
                        backgroundColor: 'rgba(255, 255, 255, 0.2)'
                    }}
                />

                <CardContent sx={{ mb: -4, px: isMobile ? 0 : 3 }}>
                    <Grid item container direction='row' alignItems='center'>
                        <Grid item xs={6} sm={7}>
                            <Stack direction='row' spacing={0} alignItems='center'>
                                <Item
                                    sx={{ boxShadow: 0, mr: 0 }}
                                    style={{ backgroundColor: 'transparent' }}>
                                    <img
                                        src={token1Icon()}
                                        alt=''
                                        width={isMobile ? 32 : 48}
                                        height={isMobile ? 32 : 48}
                                    />
                                </Item>
                                <Item
                                    sx={{ boxShadow: 0, ml: -2, mr: 1 }}
                                    style={{ backgroundColor: 'transparent' }}>
                                    <img
                                        src={token2Icon()}
                                        alt=''
                                        width={isMobile ? 32 : 48}
                                        height={isMobile ? 32 : 48}
                                    />
                                </Item>
                                <Item
                                    sx={{ boxShadow: 0, backgroundColor: 'transparent' }}>
                                    <Typography
                                        sx={{
                                            fontSize: isMobile ? 12 : 24,
                                            fontWeight: 'bold'
                                        }}
                                        color='text.primary'
                                        gutterBottom>
                                        {token1Symbol()} / {token2Symbol()}:
                                    </Typography>
                                </Item>
                            </Stack>
                            <Typography
                                sx={{
                                    fontSize: isMobile ? 13 : 16,
                                    fontWeight: 'normal'
                                }}
                                color='text.primary'>
                                Your Pool Share:
                            </Typography>
                            <Typography
                                sx={{
                                    fontSize: isMobile ? 13 : 16,
                                    fontWeight: 'normal'
                                }}
                                color='text.primary'>
                                Pooled {token1Symbol()}:
                            </Typography>
                            <Typography
                                sx={{
                                    fontSize: isMobile ? 13 : 16,
                                    fontWeight: 'normal'
                                }}
                                color='text.primary'>
                                Pooled {token2Symbol()}:
                            </Typography>
                        </Grid>
                        <Grid item xs={6} sm={5} textAlign='right'>
                            <Typography
                                sx={{
                                    fontSize: isMobile ? 14 : 24,
                                    fontWeight: 'normal',
                                    lineHeight: isMobile ? 2.5 : 1.5
                                }}
                                color='text.primary'
                                gutterBottom>
                                {numFormat(userBalance())}
                            </Typography>
                            <Typography
                                sx={{ fontSize: isMobile ? 13 : 16, fontWeight: 'bold' }}
                                color='text.primary'>
                                {numFormat(userPoolShare(), true)}%
                            </Typography>
                            <Typography
                                sx={{ fontSize: isMobile ? 13 : 16, fontWeight: 'bold' }}
                                color='text.primary'>
                                {numFormat(userToken1Amount())}
                            </Typography>
                            <Typography
                                sx={{ fontSize: isMobile ? 13 : 16, fontWeight: 'bold' }}
                                color='text.primary'>
                                {numFormat(userToken2Amount())}
                            </Typography>
                        </Grid>
                    </Grid>
                </CardContent>
            </Card>
            <CardActions>
                <Grid item container justifyContent='left'>
                    <StyledLoadingButton
                        sx={{ maxWidth: 'true', mb: -1, ml: -1 }}
                        onClick={handleClickWithdraw}>
                        <StyledButtonTypography sx={{ fontSize: isMobile ? 12 : 20 }}>
                            WITHDRAW
                        </StyledButtonTypography>
                    </StyledLoadingButton>
                    {renderRemoveLiquidity()}
                </Grid>
                <Grid item container justifyContent='right'>
                    <StyledLoadingButton
                        sx={{ maxWidth: 'true', mb: -1, mr: -1 }}
                        onClick={handleAddMore}>
                        <StyledButtonTypography sx={{ fontSize: isMobile ? 12 : 20 }}>
                            ADD LIQUIDITY
                        </StyledButtonTypography>
                    </StyledLoadingButton>
                </Grid>
            </CardActions>
        </Box>
    )
}

export default LiquidityPool
