import ReactGA from 'react-ga'

import { walletAddressState } from '../atoms'
import { getRecoil } from 'recoil-nexus'

var is_ga_initialized = false

export const GAinit = () => {
    // Google Analytics Set Up
    is_ga_initialized = true
    const gaTrackingId = 'UA-215866683-1'
    ReactGA.initialize(gaTrackingId)
}

export const GApage = (page) => {
    if (!is_ga_initialized) GAinit()
    ReactGA.pageview(page)
    GAtrack('page', page, '')
}

export const GAtrack = (cat, action, label) => {
    if (!is_ga_initialized) GAinit()
    ReactGA.event({
        category: cat,
        action: action,
        label: label
    })

    if (cat === 'TxStatus' || cat === 'NewTx' || cat === 'wallet' || cat === 'page') {
        let wa = getRecoil(walletAddressState)
        if (wa !== '') {
            // wallet is connected
            // create a new event
            ReactGA.event({
                category: 'user_flow',
                action: wa,
                label: action + ':' + label + ':' + Date.now()
            })

            if (action === 'REJECTED') {
                // special handling of rejects
                ReactGA.event({
                    category: 'rejectedTx',
                    action: wa,
                    label: label + ':' + Date.now()
                })
            }
        }
    }
}

export const GAbutton = (label) => {
    GAtrack('button', 'click', label)
}
