import React, { useState } from 'react'
import { setRecoil } from 'recoil-nexus'
import { swapSlippage, addLiquiditySlippage, withdrawLiquiditySlippage } from '../atoms'
import {
    MenuItem,
    Typography,
    AppBar,
    Box,
    Toolbar,
    IconButton,
    Input,
    Button,
    Grid,
    Divider,
    InputAdornment,
    Menu,
    FormControl,
    FormHelperText
} from '@mui/material'
import SettingsIcon from '@mui/icons-material/Settings'
import useIsMobile from '../hooks/useIsMobile'

export const DEFAULT_SLIPPAGE = '0.50'

function BoxHeader(props) {
    const [slippage, setSlippage] = useState(DEFAULT_SLIPPAGE)
    const [anchorEl, setAnchorEl] = useState(null)
    const [inputError, setInputError] = useState(false)

    const isMobile = useIsMobile()

    const handleSlippageChange = (event) => {
        let value = event.target.value
        const regex = /^(([0-9]+[.]?[0-9]*)|([.][0-9]+))$/

        if (value === '') {
            setSlippage('0')
            return
        }
        if (
            regex.test(value) &&
            value.length <= 6 &&
            Number(value) >= 0 &&
            Number(value) <= 50
        ) {
            setSlippage(value)
            setInputError(false)
        } else {
            setInputError(true)
        }
    }

    const handleMenu = (event) => {
        setAnchorEl(event.currentTarget)
    }

    const handleClose = () => {
        if (props.name === 'Swap') setRecoil(swapSlippage, slippage)
        if (props.name === 'Add Liquidity') setRecoil(addLiquiditySlippage, slippage)
        if (props.name === 'Remove Liquidity')
            setRecoil(withdrawLiquiditySlippage, slippage)

        console.log('Slippage set to: ', slippage)
        setInputError(false)
        setAnchorEl(null)
    }

    return (
        <Box sx={{ flexGrow: 1 }}>
            <AppBar
                position='static'
                sx={{
                    boxShadow: 4,
                    borderRadius: 1,
                    backgroundColor: '#3649AC'
                }}>
                <Toolbar>
                    <IconButton
                        size='large'
                        edge='start'
                        color='inherit'
                        aria-label='menu'
                        onClick={handleMenu}
                        sx={{ mr: 2, position: 'absolute', right: '0%' }}>
                        <SettingsIcon />
                    </IconButton>
                    <Typography
                        sx={{
                            flexGrow: 1,
                            fontSize: isMobile ? 16 : 24,
                            fontWeight: 'bold'
                        }}>
                        {props.name}
                    </Typography>
                    <div>
                        <Menu
                            id='menuAppBar'
                            sx={{ boxShadow: 1 }}
                            anchorEl={anchorEl}
                            anchorOrigin={{
                                vertical: 'top',
                                horizontal: 'right'
                            }}
                            keepMounted
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'right'
                            }}
                            PaperProps={{
                                style: {
                                    width: 250,
                                    backgroundColor: '#3c3966'
                                }
                            }}
                            open={Boolean(anchorEl)}
                            onClose={handleClose}>
                            <MenuItem sx={{ fontSize: 20, fontWeight: 'bold', mb: -1 }}>
                                Transaction Settings
                            </MenuItem>
                            <Divider
                                variant='middle'
                                sx={{
                                    borderBottomWidth: 1,
                                    backgroundColor: 'rgba(255, 255, 255, 0.2)'
                                }}
                            />
                            <MenuItem disableRipple={true}>
                                Set Slippage:
                                <FormControl variant='standard'>
                                    <Input
                                        sx={{
                                            ml: 3,
                                            mb: -0.5,
                                            ':before': {
                                                borderBottomColor: '1px solid white'
                                            },
                                            // underline when selected
                                            ':after': {
                                                borderBottomColor: '1px solid white'
                                            }
                                        }}
                                        value={slippage}
                                        error={inputError}
                                        size='small'
                                        onChange={handleSlippageChange}
                                        onKeyPress={(event) => {
                                            if (event.key === 'Enter') handleClose()
                                        }}
                                        disableUnderline={false}
                                        endAdornment={
                                            <InputAdornment
                                                position='end'
                                                sx={{
                                                    mr: 0,
                                                    mb: 0.5,
                                                    color: 'rgba(255, 255, 255, 0.6)'
                                                }}>
                                                %
                                            </InputAdornment>
                                        }
                                        inputProps={{
                                            type: 'string',
                                            autoComplete: 'off',
                                            style: { textAlign: 'right' },
                                            'aria-labelledby': 'input-slider'
                                        }}
                                    />
                                    {inputError && (
                                        <FormHelperText
                                            id='standard-helper-text'
                                            sx={{ color: 'red', ml: 6 }}>
                                            {slippage.length < 6
                                                ? 'Max val = 50'
                                                : 'Char limit = 6'}
                                        </FormHelperText>
                                    )}
                                </FormControl>
                            </MenuItem>
                            <Grid item container justifyContent='center'>
                                <Button
                                    variant='contained'
                                    size='small'
                                    sx={{
                                        textWeight: 'bold',
                                        mt: 1.5,
                                        backgroundColor: '#181d55',
                                        ':hover': {
                                            backgroundColor: 'rgba(24, 29, 85, 0.8)',
                                            color: 'white'
                                        }
                                    }}
                                    onClick={handleClose}>
                                    OK
                                </Button>
                            </Grid>
                        </Menu>
                    </div>
                </Toolbar>
            </AppBar>
        </Box>
    )
}

export default BoxHeader
