import React from 'react'

import {
    FormControl,
    InputLabel,
    Select,
    Typography,
    Grid,
    MenuItem
} from '@mui/material'

import { getWalletTokenAmount } from '../services/amm'

import Balance from './Balance.js'
import useIsMobile from '../hooks/useIsMobile'

function TokenSelector(props) {
    let token = props.token
    let tokenStr = props.tokenStr
    let otherToken = props.otherToken
    let tokens = props.tokens
    let handleTokenSelect = props.handleTokenSelect
    let showMaxButton = props.showMaxButton
    let handleTokenAmountChange = props.handleTokenAmountChange

    const isMobile = useIsMobile()

    const menuItems = (otherSelectedToken) => {
        let r = []
        tokens.forEach((value, address) => {
            if (otherSelectedToken !== address) {
                r.push(
                    <MenuItem key={address} value={address}>
                        <Grid container direction='row'>
                            <Grid sx={{ mr: 1, mb: 0 }}>
                                <img
                                    src={value.icon}
                                    alt=''
                                    width={isMobile ? 32 : 56}
                                    height={isMobile ? 32 : 56}
                                />
                            </Grid>
                            <Grid>
                                <Typography
                                    fontSize={isMobile ? '16px !important' : 32}
                                    fontWeight='bold'
                                    sx={{ mt: 1, mb: 0 }}>
                                    {value.symbol}
                                </Typography>
                            </Grid>
                        </Grid>
                    </MenuItem>
                )
            }
        })
        return r
    }

    return (
        <div>
            <FormControl fullWidth style={{ left: isMobile ? -12 : 0 }}>
                <InputLabel
                    id='token-label'
                    shrink={false}
                    sx={{
                        fontSize: isMobile ? 13 : 22,
                        fontWeight: 'bold',
                        mt: -0.5,
                        lineHeight: '30px'
                    }}>
                    {token === '' && 'Select ' + tokenStr}
                </InputLabel>
                {token !== '' && (
                    <Typography
                        fontSize={12}
                        sx={{ color: '#6f6e8f', ml: isMobile ? 6.7 : 9.7, mb: -3.5 }}>
                        {tokenStr + ':'}
                    </Typography>
                )}
                <Select
                    labelId='token-label'
                    id='token-select'
                    value={token}
                    label={tokenStr}
                    onChange={handleTokenSelect}
                    sx={{
                        mt: 0,
                        mb: -3,
                        '&:hover': {
                            '&& fieldset': {
                                border: '0px solid green',
                                color: 'white'
                            }
                        },
                        '.MuiOutlinedInput-notchedOutline': {
                            border: ' 0px'
                        },
                        '.MuiSelect-icon': {
                            fill: '#d0d0d0',
                            marginRight: isMobile ? '0' : '10px'
                        }
                    }}
                    MenuProps={{
                        fontSize: '16px',
                        PaperProps: {
                            sx: {
                                border: '1px solid',
                                borderColor: '#6f6e8f',
                                '& .MuiMenuItem-root:hover': {
                                    backgroundColor: '#0d0c1e'
                                },
                                '& .MuiMenu-list': {
                                    backgroundColor: '#201e43'
                                },
                                '& img': {
                                    width: isMobile ? '36px' : '40px',
                                    height: isMobile ? '36px' : '40px',
                                    margin: isMobile ? 0 : '8px',
                                    mb: isMobile ? 0 : '-20px'
                                },
                                '& .MuiTypography-root': {
                                    fontSize: isMobile ? 13 : 24
                                },
                                '& .Mui-focused .MuiOutlinedInput-notchedOutline': {
                                    border: '0px'
                                }
                            }
                        }
                    }}>
                    {menuItems(otherToken)}
                </Select>
            </FormControl>
            <Balance
                token={token}
                amount={getWalletTokenAmount(token)}
                showMaxButton={showMaxButton}
                setMaxAmountHandler={handleTokenAmountChange}
            />
        </div>
    )
}

export default TokenSelector
