import { atom, selector, selectorFamily } from 'recoil'
import { DEFAULT_SLIPPAGE } from './components/BoxHeader'

//const { persistAtom } = recoilPersist() // not currently used

export interface Pool {
    id: number
    name: string
    token1: string
    token1Amount: string // dynamic TODO: | undefined
    token2: string
    token2Amount: string // dynamic TODO: | undefined
    fee: number
    shares: string
    cfmm: number
    refreshAt: Map<string, number> // map from field to its refresh time
}

// static Token data
export interface Token {
    name: string
    symbol: string
    address: string
    decimals: number
    id: number
    icon: string // url
}

// dynamic Token data
export interface TokenUserData {
    address: string
    ammBalance: string | undefined
    userBalance: string | undefined
    userApproved: string | undefined
    refreshAt: Map<string, number>
}

export interface Transaction {
    hash: string
    type: string
    token1: string
    token1Amount: string
    token2: string
    token2Amount: string
    status: string
    dismiss: boolean // TODO : remove
    pool_id: number
    // TODO add acceptance time
}

export interface ToastMsg {
    msg: string
    type: string
    dismiss: boolean
    hash: string // for uniqueness
}

export const walletAddressState = atom({
    key: 'walletAddressState',
    default: ''
})

export const isWalletConnectedState = atom({
    key: 'isWalletConnectedState',
    default: 0 // 0-not connected, 1=being connected, 2=connected
})

export const poolsState = atom({
    key: 'poolsState',
    //    effects_UNSTABLE: [persistAtom],
    default: new Map<number, Pool>()
})

export const tokensState = atom({
    key: 'tokensState',
    //  effects_UNSTABLE: [persistAtom],
    default: new Map<string, Token>()
})

export const tokensUserDataState = atom({
    key: 'tokensUserDataState',
    default: new Map<string, TokenUserData>()
})

export const pendingTxsState = atom({
    key: 'pendingTxsState',
    default: new Array<Transaction>()
})

export const toastsState = atom({
    key: 'toastsState',
    default: new Array<ToastMsg>()
})

export const lpBalanceState = atom({
    key: 'lpBalanceState',
    default: new Map<number, number>()
})

export const swapSlippage = atom({
    key: 'swapSlippage',
    default: DEFAULT_SLIPPAGE
})

export const addLiquiditySlippage = atom({
    key: 'addLiquiditySlippage',
    default: DEFAULT_SLIPPAGE
})

export const withdrawLiquiditySlippage = atom({
    key: 'withdrawLiquiditySlippage',
    default: DEFAULT_SLIPPAGE
})

export const numOfPoolsState = atom({
    key: 'numOfPoolsState',
    default: 0
})


// Selectors

export const transactionsStatusSelector = selectorFamily ({
    key: 'transactionsStatusSelector',
    get: (componentName: string) => ({ get }): Array<Transaction> | undefined => {
        let pendingTx = get(pendingTxsState)
        if (pendingTx)
            return pendingTx.filter(t => t.type === componentName)
        return undefined
    }
})

export const walletAmountsSelector = selector({
    key: 'walletAmountsSelector',
    get: ({ get }): Map<string, string> => {
        let r = new Map<string, string>()
        get(tokensUserDataState).forEach((tud, address) => {
            if (tud.userBalance !== undefined) {
                let token = get(tokensState).get(address)
                if (token && token.decimals) {
                    r.set(address, tud.userBalance)
                }
            }
        })
        return r
    }
})

export const walletAllowanceSelector = selector({
    key: 'walletAllowanceSelector',
    get: ({ get }): Map<string, string> => {
        let r = new Map<string, string>()
        get(tokensUserDataState).forEach((tud, address) => {
            if (tud.userApproved !== undefined) {
                let token = get(tokensState).get(address)
                if (token && token.decimals) {
                    r.set(address, tud.userApproved)
                }
            }
        })
        return r
    }
})

export const pendingTxSelector = selector({
    key: 'pendingTxSelector',
    get: ({ get }): Map<string, string> => {
        let r = new Map<string, string>()
        get(pendingTxsState).forEach((tx) => {
            r.set(tx.hash, tx.status)
        })
        return r
    }
})

export const lastTxDoneSelector = selector({
    key: 'lastTxDoneSelector',
    get: ({ get }): boolean => {
        let done = true
        get(pendingTxsState).forEach((tx) => {
            done = false
            if (tx.status === 'REJECTED' || tx.status.includes('ACCEPTED')) done = true
        })
        return done
    }
})

export const safeToSendTxSelector = selector({
    key: 'safeToSendTxSelector',
    get: ({ get }): boolean => {
        const n = get(pendingTxsState).length
        if (n === 0) return true

        const tx = get(pendingTxsState)[n - 1]
        if (tx.status.includes('REJECTED') || tx.status.includes('ACCEPTED') || tx.status.includes('PENDING')) return true 

        return false
    }
})
