import React, { useState } from 'react'

import { useRecoilState } from 'recoil'
import { setRecoil } from 'recoil-nexus'
import { useInterval } from 'usehooks-ts'
import {
    Grid,
    Modal,
    Box,
    AppBar,
    Typography,
    CircularProgress,
    Button
} from '@mui/material'
import { StyledLoadingButton } from './StyleCustomizations'

import { styled } from '@mui/material/styles'

import { pendingTxsState, lpBalanceState } from '../atoms'
import PendingTransaction from './PendingTransaction.js'
import {
    getTransactionUpdate,
    addToastMsg,
    forceRefreshPool,
    getLPBalance
} from '../services/amm'
import useIsMobile from '../hooks/useIsMobile'

const Backdrop = styled('div')`
    z-index: -1;
    position: fixed;
    right: 0;
    bottom: 0;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.8);
    -webkit-tap-highlight-color: transparent;
`

function PendingTransactions() {
    const [lastTx, setLastTx] = useState('')
    const [txs] = useRecoilState(pendingTxsState)
    const [open, setOpen] = useState(false)

    const isMobile = useIsMobile()

    const handleClose = () => {
        setOpen(false)
    }

    const checkAndUpdate = async (tx) => {
        if (tx.pool_id > 0 && lastTx !== tx.hash) {
            setLastTx(tx.hash)
            forceRefreshPool(tx.pool_id)
            if (tx.type.includes('liquidity')) {
                const balance = await getLPBalance(tx.pool_id, true)
                setRecoil(lpBalanceState, (curVal) => {
                    let p = new Map(curVal)
                    p.set(tx.pool_id, balance)
                    return p
                })
            }
        }
    }

    const updateTransactionsStatus = async () => {
        txs.forEach(async (tx) => {
            if (!tx.dismiss) {
                let status = await getTransactionUpdate(tx.hash)
                let name = tx.type
                    .split(/ |_/)
                    .map((x) => x[0].toUpperCase() + x.slice(1))
                    .join(' ')
                if (status !== null) {
                    console.log('tx status=', status, ' tx.pool=', tx.pool_id)
                    // should get here only if there is an update
                    if (status.includes('RECEIVED')) {
                        addToastMsg(name + ' Transaction Sent', 'success')
                    }
                    if (status.includes('REJECTED')) {
                        addToastMsg(name + ' Transaction Rejected', 'error')
                    }
                    if (status.includes('PENDING')) {
                        addToastMsg(name + ' Transaction Pending', 'success')
                        checkAndUpdate(tx)
                    }
                    if (status.includes('ACCEPTED')) {
                        addToastMsg(name + ' Transaction Accepted', 'success')
                        checkAndUpdate(tx)
                    }
                }
            }
        })
    }
    useInterval(updateTransactionsStatus, 10000)

    const pendingTransactions = () => {
        if (txs.length === 0) {
            return (
                <Typography fontSize={18} sx={{ m: 4 }}>
                    Session transactions will appear here
                </Typography>
            )
        }

        let r = []
        txs.forEach((tx) => {
            r.push(<PendingTransaction key={tx.hash} tx={tx} />)
        })
        return r
    }

    const transactionHistoryModal = () => {
        console.log('Going into Transaction History Modal')

        return (
            <div>
                <Modal
                    open={open}
                    onClose={handleClose}
                    BackdropComponent={Backdrop}
                    aria-labelledby='modal-modal-title'
                    aria-describedby='modal-modal-description'>
                    <Box
                        sx={{
                            position: 'absolute',
                            top: '50%',
                            left: '50%',
                            transform: 'translate(-50%, -50%)',
                            minWidth: isMobile ? 'calc(100% - 24px)' : '25%',
                            minHeight: '25%',
                            backgroundColor: '#0d0c1e',
                            border: '1px solid rgba(255, 255, 255, 0.27)',
                            boxShadow: 40,
                            borderRadius: isMobile ? 0.75 : 2,
                            p: isMobile ? 1.5 : 3
                        }}>
                        <AppBar
                            position='static'
                            sx={{
                                boxShadow: 40,
                                borderRadius: 0.7,
                                backgroundColor: '#3649AC'
                            }}>
                            <Typography
                                fontSize={isMobile ? 16 : 24}
                                fontWeight='bold'
                                sx={{ textAlign: 'center', mt: 1, mb: 1 }}>
                                Transaction History
                            </Typography>
                        </AppBar>
                        <Grid
                            container
                            direction='column-reverse'
                            justifyContent='flex-start'
                            alignItems='center'>
                            {pendingTransactions()}
                        </Grid>
                        <Grid
                            container
                            justifyContent='center'
                            alignItems='bottom'
                            sx={{ mt: 4 }}>
                            <StyledLoadingButton
                                sx={{ width: '50%', borderRadius: 0.7 }}
                                onClick={() => setOpen(false)}>
                                <Typography>OK</Typography>
                            </StyledLoadingButton>
                        </Grid>
                    </Box>
                </Modal>
            </div>
        )
    }

    const txHistoryButton = () => {
        if (txs.length === 0) return

        let txInProgress = txs.find((e) =>
            ['new', 'RECEIVED', 'PENDING'].includes(e.status)
        )

        return (
            <Button sx={{ maxHeight: '100%' }} onClick={() => setOpen(true)}>
                <Box>
                    {txInProgress !== undefined && (
                        <CircularProgress
                            size={isMobile ? 12 : 24}
                            sx={{ color: 'white', mt: 1 }}
                        />
                    )}
                </Box>
                <Typography
                    sx={{
                        fontSize: isMobile ? 14 : 18,
                        textAlign: 'center',
                        alignItems: 'center',
                        color: 'rgba(240, 240, 240, 0.7)'
                    }}>
                    {txInProgress !== undefined ? txInProgress.status : 'TX History'}
                </Typography>
            </Button>
        )
    }

    return (
        txs.length !== 0 && (
            <Grid
                sx={{
                    mr: isMobile ? 1 : 2,
                    backgroundColor: '#302e54',
                    justifyContent: 'center',
                    borderRadius: '10px',
                    display: 'flex',
                    maxWidth: isMobile ? '102px' : '160px'
                }}>
                {txHistoryButton()}
                {transactionHistoryModal()}
            </Grid>
        )
    )
}

export default PendingTransactions
