export const numFormat = (num: string, isPercent = false): string => {
    if (num === undefined || num === '') return ''

    if (isPercent) {
        let res = BigNumber(num).shiftedBy(2).sd(4, BigNumber.ROUND_DOWN).toPrecision(4)
        if (BigNumber(res).lt(0.01)) return '<0.01'
        return res
    }

    if (BigNumber(num).lt(1)) {
        // BigNumber(0.012340000).sd(2).toString(10) --> '0.012'
        let res = BigNumber(num)
            .sd(6, BigNumber.ROUND_DOWN)
            .toFixed(18, BigNumber.ROUND_DOWN) // FIXME: need to align to token decimals (not 18).... 6 is a magic number...
        // patch:
        return BigNumber(res).toString(10)
    }
    if (BigNumber(num).gt(10 ** 10)) return BigNumber(num).toFixed(0)

    return BigNumber(num).toFixed(2, BigNumber.ROUND_DOWN)
}

export const tokenAllowanceForAMM = (token: string) => {
    let ret = null
    if (token) {
        let tokensUserData = getRecoil(tokensUserDataState)
        let tud = tokensUserData.get(token)
        if (tud) {
            ret = tud.userApproved
            // console.log('tokenAllowanceForAMM tud.userApproved = ', tud.userApproved)
        }
    }
    return ret
}

export const isTokenApproved = (token: string, amount: string): boolean => {
    try {
        let tokenAllowance = tokenAllowanceForAMM(token) // should return a string
        if (
            tokenAllowance &&
            BigNumber(tokenAllowance).gte(BigNumber(float_to_decimal_int(token, amount)))
        ) {
            return true
        }
    } catch (e) {}
    return false
}

export const checkAndUpdateComponentTransactions = (
    transactionsStatus: Array<Transaction>,
    componentTxs: Record<string, string>,
    setTransactionInProgress: (b: boolean) => any
) => {
    if (transactionsStatus) {
        transactionsStatus.forEach((tx) => {
            // check if transaction was already handled
            if (componentTxs.hasOwnProperty(tx.hash)) {
                console.log('componentTxs has hash ', tx.hash)
                if (
                    ['REJECTED', 'PENDING', 'ACCEPTED'].some((s) => tx.status.includes(s))
                ) {
                    console.log('Setting transaction in progress to false')
                    setTransactionInProgress(false)
                    delete componentTxs[tx.hash]
                }
            }
        })
    }
}

export const getPoolPriceForTokenA = (
    poolId: number,
    token_in: string,
    token_out: string,
    otherAmount = '1'
): string => {
    let pools = getRecoil(poolsState)
    let pool = pools.get(poolId)
    let ratio = BigNumber(1.0)

    if (pool) {
        // console.log('getPoolPrice: ', pool.token1, token_in, pool.token2Amount, pool.token1Amount, otherAmount)
        if (pool.token1 === token_in) {
            ratio = BigNumber(decimal_int_to_float(token_out, pool.token2Amount))
                .times(BigNumber(otherAmount))
                .div(BigNumber(decimal_int_to_float(token_in, pool.token1Amount)))
        } else {
            ratio = BigNumber(decimal_int_to_float(token_out, pool.token1Amount))
                .times(BigNumber(otherAmount))
                .div(BigNumber(decimal_int_to_float(token_in, pool.token2Amount)))
        }
    }
    //console.log('Ratio', ratio.toString(10))
    return ratio.toString(10)
}

export const getWalletTokenAmount = (token: string): string => {
    if (is_dev()) console.log('amm: getWalletTokenAmount, token=', token)

    let activeAccount: string = walletAddress()
    let tokensUserData = getRecoil(tokensUserDataState)
    let tud = tokensUserData.get(token) // expecting that tud values will be strings

    if (!activeAccount || !token || !tud || tud.userBalance === undefined) return ''

    return decimal_int_to_float(token, tud.userBalance)
}

export const isSufficientBalanceInWallet = (token: string, amount: string) => {
    if (is_dev())
        console.log('amm: isSufficientBalanceInWallet, token=', token, ' amount=', amount)

    let activeAccount: string = walletAddress()
    let tokensUserData = getRecoil(tokensUserDataState)
    let tud = tokensUserData.get(token)

    if (!activeAccount) return WALLET_SUFFICIENT_BALANCE.UNKNOWN
    if (!token) return WALLET_SUFFICIENT_BALANCE.UNKNOWN
    if (!amount) return WALLET_SUFFICIENT_BALANCE.TRUE
    if (!tud) return WALLET_SUFFICIENT_BALANCE.UNKNOWN
    if (tud.userBalance === undefined) return WALLET_SUFFICIENT_BALANCE.UNKNOWN

    // The actual check:
    let tokenWalletAmount = decimal_int_to_float(token, tud.userBalance)

    if (
        BigNumber(amount).isPositive() &&
        BigNumber(amount).lte(BigNumber(tokenWalletAmount))
    )
        return WALLET_SUFFICIENT_BALANCE.TRUE

    return WALLET_SUFFICIENT_BALANCE.FALSE
}

export const isInsufficientBalanceInPool = (
    poolId: number,
    token: string,
    floatAmount: string
): boolean => {
    if (is_dev())
        console.log(
            'amm: isInsufficientBalanceInPool, pool=',
            poolId,
            ' token=',
            token,
            ' amount=',
            floatAmount
        )

    if (!token || poolId <= 0) return false

    let pools = getRecoil(poolsState)
    let pool: Pool | undefined = pools.get(poolId)
    if (!pool) throw Error('missing pool ' + poolId)

    let decimalAmount = float_to_decimal_int(token, floatAmount)

    if (pool.token1 === token) {
        return BigNumber(decimalAmount).gt(BigNumber(pool.token1Amount))
    } else {
        return BigNumber(decimalAmount).gt(BigNumber(pool.token2Amount))
    }
}

export const addToastMsg = (msg: string, type: string) => {
    let t: ToastMsg = {
        msg: msg,
        type: type,
        dismiss: false,
        hash: BigNumber.random(10).times(1e10).toString(16)
    }
    setRecoil(toastsState, (current: Array<ToastMsg>) => {
        return [...current, t]
    })
}

