export const updateAllTokensDynamicData = async (
    force: boolean = false
): Promise<any> => {
    if (
        !force &&
        Date.now() < lastCallToUpdateAllTokens + SECS_TO_REFRESH_POOL_DATA * 1000
    )
        return false

    let tokens = getRecoil(tokensState)

    if (tokens.size === 0) return false

    lastCallToUpdateAllTokens = Date.now()

    tokens.forEach((_, token) => {
        updateTokenDynamicData(token, force)
    })
    return true
}

async function sleepNSec(timeInMS: number = 10000) {
    console.log('Entering sleep')
    return new Promise((r) => setTimeout(r, timeInMS))
}

export const updateNewPoolData = async (i: number): Promise<boolean> => {
    let now = Date.now()

    console.log('updateNewPool i=', i)

    let data = undefined
    let shares = undefined

    const contract = new Contract(ammAbi as Abi, amm_contract)

    for (let j = 0; j < 3; j++) {
        try {
            data = await contract.get_pool(i.toString())
            break
        } catch (exception) {}
        await sleepNSec()
    }

    for (let j = 0; j < 3; j++) {
        try {
            shares = await contract.get_total_shares(i.toString())
            break
        } catch (exception) {}
        await new Promise((r) => setTimeout(r, 1000))
    }

    if (!data || !shares) {
        console.log('failed to update pool ', i)
        return false
    }

    let pool: Pool = {
        id: i,
        name: hex_to_ascii(data[0].name.toString(16)),
        token1: '0x' + data[0].token_a_address.toString(16),
        token1Amount: composeUInt256(
            data[0].token_a_reserves.low,
            data[0].token_a_reserves.high
        ),
        token2: '0x' + data[0].token_b_address.toString(16),
        token2Amount: composeUInt256(
            data[0].token_b_reserves.low,
            data[0].token_b_reserves.high
        ),
        fee: Number(data[0].fee_percentage.toString()) / 1e5,
        cfmm: Number(data[0].cfmm_type.toString()),
        shares: composeUInt256(shares[0].low, shares[1].high),
        //refresh: now,
        refreshAt: new Map([
            ['shares', now],
            ['reserves', now]
        ])
    }
    setRecoil(poolsState, (curVal: Map<number, Pool>) => {
        let p = new Map(curVal)
        p.set(i, pool)
        return p
    })
    let now1 = Date.now()
    console.log('updated pool ', i, ' took ', now1 - now)
    return true
}

export const updatePoolData = async (i: number): Promise<boolean> => {
    let pools = getRecoil(poolsState)

    let currentPool: Pool | undefined = pools.get(i)
    if (currentPool === undefined) return updateNewPoolData(i)

    //console.log('updatePoolData, i=', i, ' pool=', currentPool)
    let now = Date.now()

    const contract = new Contract(ammAbi as Abi, amm_contract)

    let data = undefined
    let shares = undefined
    for (let j = 0; j < 2; j++) {
        try {
            if (
                (currentPool.refreshAt.get('reserves') || 0) >
                now - SECS_TO_REFRESH_POOL_DATA * 1000
            )
                break

            data = await contract.get_pool(i.toString())

            currentPool.name = hex_to_ascii(data[0].name.toString(16))
            currentPool.token1 = '0x' + data[0].token_a_address.toString(16)
            currentPool.token1Amount = composeUInt256(
                data[0].token_a_reserves.low,
                data[0].token_a_reserves.high
            )
            currentPool.token2 = '0x' + data[0].token_b_address.toString(16)
            currentPool.token2Amount = composeUInt256(
                data[0].token_b_reserves.low,
                data[0].token_b_reserves.high
            )
            currentPool.fee = Number(data[0].fee_percentage.toString()) / 1e5
            currentPool.cfmm = Number(data[0].cfmm_type.toString())
            currentPool.refreshAt.set('reserves', now)
            break
        } catch (exception) {}
        await sleepNSec()
    }

    setRecoil(poolsState, (curVal: Map<number, Pool>) => {
        let p = new Map(curVal)
        if (currentPool !== undefined) p.set(i, currentPool)
        return p
    })
    for (let j = 0; j < 2; j++) {
        try {
            if (
                (currentPool.refreshAt.get('shares') || 0) >
                now - SECS_TO_REFRESH_POOL_DATA * 1000
            )
                break

            shares = await contract.get_total_shares(i.toString())
            currentPool.shares = composeUInt256(shares[0].low, shares[0].high)
            currentPool.refreshAt.set('shares', now)
            break
        } catch (exception) {}
        await sleepNSec()
    }
    // console.log(currentPool)
    setRecoil(poolsState, (curVal: Map<number, Pool>) => {
        let p = new Map(curVal)
        if (currentPool !== undefined) p.set(i, currentPool)
        return p
    })

    // let now1 = Date.now()
    // console.log('updated pool ', i, ' took ', now1 - now)
    return true
}

export const updatePoolDynamicData = async (poolId: number): Promise<any> => {
    if (is_dev()) addToastMsg('updatePoolDynamicData, pool=' + poolId, 'info')

    let b = undefined
    try {
        b = updatePoolData(poolId)
    } catch (exception) {
        console.log('****************** NETWORK ERROR!!!')
    }
    if (!b) return

    let activeAccount: string = walletAddress()

    if (!activeAccount) {
        return null
    }

    let pools = getRecoil(poolsState)
    let tokens = getRecoil(tokensState)

    const starknet = getStarknet()

    // checks that enable succeeded
    if (starknet.isConnected === false) throw Error('starknet wallet not connected')

    let pool: Pool | undefined = pools.get(poolId)
    if (!pool) throw Error('unknown pool ' + poolId)

    let token1: Token | undefined = tokens.get(pool.token1)
    let token2: Token | undefined = tokens.get(pool.token2)
    if (!token1) throw Error('missing token ' + pool.token1)
    if (!token2) throw Error('missing token ' + pool.token2)

    // so far everything must have been stored in recoil
    // now we update dynamic data: per token in pool
    updateTokenDynamicData(pool.token1)
    updateTokenDynamicData(pool.token2)
}

export const updateTokenDynamicData = async (
    token_address: string,
    force: boolean = false
): Promise<any> => {
    if (!token_address) return null
    if (is_dev()) addToastMsg('updateTokenDynamicData, token=' + token_address, 'info')

    let activeAccount: string = walletAddress()

    if (!activeAccount) {
        return null
    }

    const contract = new Contract(erc20Abi as Abi, token_address)

    let tokensDynamic = getRecoil(tokensUserDataState)
    let current_token = tokensDynamic.get(token_address)
    if (current_token === undefined) {
        current_token = {
            address: token_address,
            ammBalance: undefined,
            userBalance: undefined,
            userApproved: undefined,
            refreshAt: new Map([
                ['userBalance', 0],
                ['userApproved', 0],
                ['ammBalance', 0]
            ])
        }
        // new token - first store an empty shell in Recoil, then the async requests will update it
        setRecoil(tokensUserDataState, (currentTud: Map<string, TokenUserData>) => {
            let newMap = new Map(currentTud)
            if (!newMap.get(token_address) && current_token !== undefined) {
                newMap.set(token_address, current_token)
            }
            return newMap
        })
    }

    let promises: Array<Promise<any>> = []

    for (let j = 0; j < 2; j++) {
        if (current_token !== undefined)
            console.log(
                'Delta refresh = ',
                Date.now() - (current_token.refreshAt.get('userBalance') || 0)
            )
        else console.log('It seems that current_token is undefined')

        if (
            current_token !== undefined &&
            (force ||
                (current_token.refreshAt.get('userBalance') || 0) <=
                    Date.now() - SECS_TO_REFRESH_POOL_DATA * 1000)
        ) {
            let p2 = contract
                .balanceOf(numberToCallParam(activeAccount))
                .then((data: any) => {
                    // update balance directly in recoil
                    const balance = composeUInt256(data[0].low, data[0].high)
                    setRecoil(
                        tokensUserDataState,
                        (currentTud: Map<string, TokenUserData>) => {
                            let newMap = new Map(currentTud)
                            let tud = newMap.get(token_address)
                            if (tud) {
                                tud.userBalance = balance
                                tud.refreshAt.set('userBalance', Date.now())
                                newMap.set(tud.address, tud)
                            }
                            return newMap
                        }
                    )
                    return true
                })
                .catch((reason: any) => {
                    console.log('************* NETWORK ERROR ', reason)
                    return false
                })
            promises.push(p2)
        }

        if (
            current_token !== undefined &&
            (force ||
                (current_token.refreshAt.get('userApproved') || 0) <=
                    Date.now() - SECS_TO_REFRESH_POOL_DATA * 1000)
        ) {
            let p3 = contract
                .allowance(
                    numberToCallParam(activeAccount),
                    numberToCallParam(amm_contract)
                )
                .then((data: any) => {
                    const balance = composeUInt256(data[0].low, data[0].high)
                    setRecoil(
                        tokensUserDataState,
                        (currentTud: Map<string, TokenUserData>) => {
                            let newMap = new Map(currentTud)
                            let tud = newMap.get(token_address)
                            if (tud) {
                                tud.userApproved = balance
                                tud.refreshAt.set('userApproved', Date.now())
                                newMap.set(tud.address, tud)
                            }
                            return newMap
                        }
                    )
                    return true
                })
                .catch((reason: any) => {
                    console.log('************* NETWORK ERROR ', reason)
                    return false
                })
            promises.push(p3)
        }
        let pass = true
        for (let k = 0; k < promises.length; k++) {
            let b = await promises[k]
            if (!b) pass = false
        }
        if (pass) break
        await sleepNSec()
    }
}

function addPendingTx(
    hash: string,
    type: string,
    ga_label: string,
    pool_id: number,
    token1Address: string,
    token1Amount: string = '',
    token2Address: string = '',
    token2Amount: string = ''
) {
    GAtrack('NewTx', ga_label, hash)

    // Clear Dismissed and Rejected txs
    setRecoil(pendingTxsState, (currTxs: Array<Transaction>) => {
        return currTxs.filter((t) => !t.status.includes('REJECTED'))
    })

    // fixme: use 'type' as enum and not freestyle strings
    let tx: Transaction = {
        hash: hash,
        type: type,
        token1: token1Address,
        token1Amount: token1Amount,
        token2: token2Address,
        token2Amount: token2Amount,
        status: 'new',
        dismiss: false,
        pool_id: pool_id
    }
    setRecoil(pendingTxsState, (current: Array<Transaction>) => {
        return [...current, tx]
    })
}

export const approve = async (
    tokenFrom: string,
    amount: string,
    label: string
): Promise<any> => {
    console.log('Entered approve')

    const starknet = getStarknet()
    let activeAccount: string = walletAddress()

    if (!activeAccount) {
        return null
    }

    if (starknet.isConnected === false) throw Error('starknet wallet not connected')

    if (!safeToSend()) return null

    console.log('activeAccount=', activeAccount)
    const contract = new Contract(erc20Abi as Abi, tokenFrom, starknet.account)
    let tx
    try {
        tx = await contract.approve(numberToCallParam(amm_contract), [
            numberToCallParam(amount),
            numberToCallParam(0)
        ])
    } catch (error) {
        console.log('Error in approve', error)
        throw error
    }

    console.log('Adding transaction - approve ', tx)
    addPendingTx(tx.transaction_hash, 'approve', label, 0, tokenFrom, amount)

    return tx
}

export const ERC20_mint = async (token: string, amount: string): Promise<any> => {
    const starknet = getStarknet()

    let activeAccount: string = walletAddress()

    if (!activeAccount) {
        return null
    }
    if (starknet.isConnected === false) throw Error('starknet wallet not connected')

    if (!safeToSend()) return null

    const contract = new Contract(erc20Abi as Abi, token, starknet.account)
    let tx = await contract.mint(
        numberToCallParam(activeAccount),
        numberToCallParam(amount),
        numberToCallParam(0)
    )

    addPendingTx(tx.transaction_hash, 'mint', 'mint', 0, token, amount)

    return tx
}

