export const getLPBalance = async (
    pool_id: number,
    force: boolean = false
): Promise<any> => {
    if (
        !force &&
        (lastCallToUpdateLPBalance.get(pool_id) || 0) >
            Date.now() - SECS_TO_REFRESH_POOL_DATA * 1000
    )
        return

    const starknet = getStarknet()

    let address: string = walletAddress()
    if (!address) {
        return null
    }

    if (starknet.isConnected === false) throw Error('starknet wallet not connected')

    lastCallToUpdateLPBalance.set(pool_id, Date.now())

    const contract = new Contract(ammAbi as Abi, amm_contract)
    for (let j = 0; j < 2; j++) {
        try {
            let data = await contract.get_lp_balance(
                numberToCallParam(pool_id),
                numberToCallParam(address)
            )
            const balance = composeUInt256(data[0].low, data[0].high)
            return balance
        } catch {
            await sleepNSec()
        }
    }
    return undefined
}

export const composeUInt256 = (low: string, high: string): string => {
    return BigNumber(low)
        .plus(BigNumber(high).times(BigNumber(2).pow(128)))
        .toString(10)
}

// fixme: probably redundant, consider remove
const numberToCallParam = (n: number | string): string => {
    return BigNumber(n).toString(10)
}

// fixme substr is deprecated
function hex_to_ascii(str1: string): string {
    var hex = str1
    var str = ''
    for (var n = 0; n < hex.length; n += 2) {
        str += String.fromCharCode(parseInt(hex.substr(n, 2), 16))
    }
    return str
}

export function humanReadableHash(hash: string, isMobile: boolean): string {
    if (!hash) {
        return ''
    }
    if (hash.length <= 8) {
        return hash
    }
    if (isMobile) {
        return hash.substr(0, 4) + '...' + hash.substr(-2)
    }
    return hash.substr(0, 5) + '...' + hash.substr(-3)
}

