export const getTransactionUpdate = async (hash: string): Promise<string | null> => {
    let t = await getStarknet().provider.getTransactionStatus(hash)

    // check if transaction status has changed and update Recoil
    let pendingTxs = getRecoil(pendingTxsState)
    if (
        pendingTxs &&
        pendingTxs.find((tx) => tx.hash === hash && tx.status !== t.tx_status)
    ) {
        console.log('getTransactionUpdate: ', t.tx_status, hash)
        GAtrack('TxStatus', t.tx_status, hash)

        setRecoil(pendingTxsState, (currTxs: Array<Transaction>) => {
            let r = new Array<Transaction>()
            currTxs.forEach((tx) => {
                console.log('Inside forEach: ', tx.hash, tx.status)
                if (tx.hash === hash) {
                    // dup and change
                    let tx_: Transaction = Object.assign({}, tx)
                    tx_.status = t.tx_status
                    if (
                        //tx_.status === 'PENDING' ||
                        // tx_.status === 'ACCEPTED_ON_L2' ||
                        tx_.status === 'ACCEPTED_ON_L1'
                    )
                        tx_.dismiss = true

                    // we can delete the transaction here if on L1...

                    r.push(tx_)
                } else {
                    r.push(tx)
                }
            })
            return r
        })
        return t.tx_status
    }
    return null
}

function sortTokens(token1: string, token2: string): string[] {
    return BigNumber(token1).lt(BigNumber(token2)) ? [token1, token2] : [token2, token1]
}

function getPoolTokens(poolId: number): string[] {
    let pools = getRecoil(poolsState)
    if (!pools) throw Error('Pools not avail')

    let pool: Pool | undefined = pools.get(poolId)
    if (!pool) throw Error('unknown pool ' + poolId)
    return [pool.token1, pool.token2]
}

function getPoolOtherToken(poolId: number, token: string): string {
    let pools = getRecoil(poolsState)
    if (!pools) return ''

    let pool: Pool | undefined = pools.get(poolId)
    if (!pool) throw Error('unknown pool ' + poolId)

    return pool.token1 === token ? pool.token2 : pool.token1
}

export const getPoolIdFromTokens = (token1: string, token2: string): number => {
    // check that we know these tokens
    let pools = getRecoil(poolsState)
    let tokens = getRecoil(tokensState)
    if (!tokens.get(token1) || !tokens.get(token2)) throw Error('Unknown tokens')

    let [t1, t2] = sortTokens(token1, token2)
    for (let id = 1; id <= pools.size; id++) {
        let pool: Pool | undefined = pools.get(id)
        if (!pool) throw Error('missing pool ' + id)
        if (pool.token1 === t1 && pool.token2 === t2) return id
    }
    return 0
}

export const forceRefreshPool = (poolId: number) => {
    console.log('force refreshing pool ' + poolId)
    setRecoil(poolsState, (curVal: Map<number, Pool>) => {
        let p = new Map(curVal)
        let pool = p.get(poolId)
        if (pool) {
            pool.refreshAt.set('reserves', 0)
            pool.refreshAt.set('shares', 0)
            p.set(poolId, pool)
        }
        return p
    })
    // also tokens
    let pool = getRecoil(poolsState).get(poolId)
    if (pool !== undefined) {
        setRecoil(tokensUserDataState, (currentTud: Map<string, TokenUserData>) => {
            let newMap = new Map(currentTud)
            if (pool !== undefined) {
                let t1 = newMap.get(pool.token1)
                if (t1) {
                    t1.refreshAt.set('userBalance', 0)
                    t1.refreshAt.set('userApproved', 0)
                }
                let t2 = newMap.get(pool.token2)
                if (t2) {
                    t2.refreshAt.set('userBalance', 0)
                    t2.refreshAt.set('userApproved', 0)
                }
            }
            return newMap
        })
    }
    updatePoolDynamicData(poolId)
}

export const updateUserLPBalance = async (force: boolean = false) => {
    console.log('updateUserLPBalance')
    for (let i = 1; i <= getRecoil(numOfPoolsState); i++) {
        const balance = await getLPBalance(i, force)
        if (balance) {
            console.log('updateUserLPBalance: Rerturned balance ', balance, i)
            setRecoil(lpBalanceState, (curVal) => {
                let p = new Map(curVal)
                p.set(i, balance)
                return p
            })
        }
    }
}

export const isPoolUpToDate = (poolId: number, checkShares: boolean = true): boolean => {
    let res = false
    let pools = getRecoil(poolsState)
    if (pools.has(poolId)) {
        const pool = pools.get(poolId)
        if (pool !== undefined) {
            if (checkShares) {
                res =
                    (pool.refreshAt.get('reserves') || 0) >= Date.now() - 1000 * 3600 &&
                    (pool.refreshAt.get('shares') || 0) >= Date.now() - 1000 * 3600
            } else {
                res = (pool.refreshAt.get('reserves') || 0) >= Date.now() - 1000 * 3600
            }
            console.log(
                'isPoolUpToDate: pool res=',
                res,
                ' pool refresh=',
                pool.refreshAt,
                ' poolId=',
                poolId
            )
            if (res) {
                // also check tokens
                console.log('checking tokens up to date')
                let tokens = getRecoil(tokensUserDataState)
                let t1 = tokens.get(pool.token1)
                let t2 = tokens.get(pool.token2)
                if (t1 && t2) {
                    let a = [
                        t1.refreshAt.get('userBalance') || 0,
                        t1.refreshAt.get('userApproved') || 0,
                        t2.refreshAt.get('userBalance') || 0,
                        t2.refreshAt.get('userApproved') || 0
                    ]

                    res = Math.min(...a) >= Date.now() - 1000 * 3600
                    console.log('tokens up to date: ', res)
                } else {
                    res = false
                }
            }
        }
    }
    if (!res) addToastMsg('Waiting for Starknet, try again later', 'error')
    return res
}

export function float_to_decimal_int(token_address: string, amount: string): string {
    let tokens = getRecoil(tokensState)
    let token: Token | undefined = tokens.get(token_address)
    if (token === undefined) {
        // throw Error('undefined pool')
        addToastMsg(
            'float_to_decimal_int: Undefined pool for token ' +
                token_address +
                ', tokens size=' +
                tokens.size +
                ' amount=' +
                amount,
            'warning'
        )
        return '0'
    }

    return BigNumber(amount).shiftedBy(token.decimals).toFixed(0, BigNumber.ROUND_DOWN)
}

export function decimal_int_to_float(token_address: string, amount: string): string {
    let tokens = getRecoil(tokensState)
    let token: Token | undefined = tokens.get(token_address)
    if (token === undefined) {
        //throw Error('undefined pool')
        addToastMsg(
            'decimal_int_to_float: Undefined pool for token ' +
                token_address +
                ', tokens size=' +
                tokens.size +
                ' amount=' +
                amount,
            'warning'
        )
        return '0'
    }

    return BigNumber(amount).shiftedBy(-token.decimals).toString(10) // should have exactly 'decimals' floating point
}

export const calcOtherTokenAmount = (
    token_leading: string,
    token_other: string,
    token_leading_amount_float: string,
    leading_token_number: number, // either 1, or 2
    with_fee: Boolean = true
): string | undefined => {
    let poolId = getPoolIdFromTokens(token_leading, token_other)
    if (
        poolId === 0 ||
        token_leading_amount_float === undefined ||
        token_leading_amount_float === null
    )
        return undefined

    if (token_leading_amount_float === '') return ''

    let token_leading_amount = float_to_decimal_int(
        token_leading,
        token_leading_amount_float
    )

    let pools = getRecoil(poolsState)
    let pool: Pool | undefined = pools.get(poolId)

    if (pool === undefined) {
        addToastMsg('Undefined pool id=' + poolId, 'warning')
        return undefined
    }

    // check reserves
    let token_leading_reserve = '0'
    let token_other_reserve = '0'

    if (pool.token1 === token_leading) {
        token_leading_reserve = pool.token1Amount
        token_other_reserve = pool.token2Amount
    } else {
        token_leading_reserve = pool.token2Amount
        token_other_reserve = pool.token1Amount
    }

    console.log(
        'CalcOtherTokenAmount: ',
        token_leading,
        token_other,
        token_leading_amount_float,
        leading_token_number,
        token_leading_reserve,
        token_other_reserve
    )
    if (BigNumber(token_leading_reserve).lte(0) || BigNumber(token_other_reserve).lte(0))
        throw Error('token reserve cannot be 0')

    // amount * (1-fee)
    let pool_fee = with_fee ? BigNumber(pool.fee.toString()) : BigNumber('0')
    let calc_fee = BigNumber(1).minus(pool_fee)
    if (calc_fee.eq(0)) throw Error('Fee is set to 100%')

    if (leading_token_number === 1) {
        let token_amount_after_fee = BigNumber(token_leading_amount).times(calc_fee)
        let token_out_expected_amount = BigNumber(token_other_reserve)
            .times(BigNumber(token_amount_after_fee))
            .div(BigNumber(token_leading_reserve).plus(BigNumber(token_amount_after_fee)))
            .toFixed(0, BigNumber.ROUND_DOWN)

        return decimal_int_to_float(token_other, token_out_expected_amount)
    } else {
        // leading token is 2, calc what should be the output in order to get
        let token_in_calculated_amount = BigNumber(token_other_reserve)
            .times(BigNumber(token_leading_amount))
            .div(BigNumber(token_leading_reserve).minus(BigNumber(token_leading_amount)))
            .div(calc_fee)
            .toFixed(0, BigNumber.ROUND_UP)

        return decimal_int_to_float(token_other, token_in_calculated_amount)
    }
}

export const calcOtherTokenRequiredAmountForLiquidity = (
    token_a: string,
    token_b: string,
    token_a_float: string
): string | undefined => {
    let poolId = getPoolIdFromTokens(token_a, token_b)
    if (poolId === 0 || token_a_float === undefined || token_a_float === null)
        return undefined

    if (token_a_float === '') return ''

    // Calcs are done in float
    let token_a_amount = token_a_float

    let pools = getRecoil(poolsState)
    let pool: Pool | undefined = pools.get(poolId)

    if (pool === undefined) {
        // throw Error('undefined pool')
        addToastMsg('Undefined pool id=' + poolId, 'warning')
        return undefined
    }

    let price = getPoolPriceForTokenA(poolId, token_a, token_b, token_a_amount)

    console.log('Other Token Add Liquidity: ', price, token_a_amount)
    //let token_b_amount = BigNumber(price).times(BigNumber(token_a_amount)).toFixed(0, BigNumber.ROUND_DOWN)
    //return decimal_int_to_float(token_b, token_b_amount)

    return decimal_int_to_float(token_b, float_to_decimal_int(token_b, price))
}

