import React, { useState, useEffect } from 'react'
import { useRecoilState } from 'recoil'
import { getRecoil } from 'recoil-nexus'
import { BigNumber } from 'bignumber.js'

import {
    tokensState,
    poolsState,
    tokensUserDataState,
    walletAddressState,
    addLiquiditySlippage,
    transactionsStatusSelector
} from '../atoms'

import {
    add_liquidity,
    updateTokenDynamicData,
    updatePoolDynamicData,
    getPoolIdFromTokens,
    decimal_int_to_float,
    float_to_decimal_int,
    addToastMsg,
    getPoolPriceForTokenA,
    numFormat,
    calcOtherTokenRequiredAmountForLiquidity,
    checkAndUpdateComponentTransactions,
    isSufficientBalanceInWallet,
    WALLET_SUFFICIENT_BALANCE,
    isPoolUpToDate,
    forceRefreshPool,
    TST_TOKEN_ADDRESS,
    TST_TOKEN_MAX_ALLOWED
} from '../services/amm'

import {
    FormControl,
    TextField,
    Grid,
    Box,
    Card,
    CardContent,
    Divider,
    Typography
} from '@mui/material'
import MonetizationOnIcon from '@mui/icons-material/MonetizationOn'
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet'

import { useInterval } from 'usehooks-ts'

import { GApage, GAbutton } from '../services/ga.js'

import BoxHeader from './BoxHeader.js'
import Positions from './Positions.js'
import TokenSelector from './TokenSelector.js'
import ConnectWallet from './ConnectWallet.js'
import { StyledLoadingButton, StyledButtonTypography } from './StyleCustomizations'
import useIsMobile from '../hooks/useIsMobile'

function Provide() {
    const [tokens] = useRecoilState(tokensState)
    const [pools] = useRecoilState(poolsState)
    const [token1, setToken1] = useState('')
    const [token1Amount, setToken1Amount] = useState('')
    const [token2, setToken2] = useState('')
    const [token2Amount, setToken2Amount] = useState('')
    const [activePoolId, setActivePoolId] = useState(0)
    const [connectedAddress] = useRecoilState(walletAddressState)
    const [addLiquidityInProgress, setAddLiquidityInProgress] = useState(false)
    const [leadingToken, setLeadingToken] = useState(0) // 0 is no token, 1/2 is the last updated token
    const [transactionsStatus] = useRecoilState(
        transactionsStatusSelector('add_liquidity')
    )
    const [tuds] = useRecoilState(tokensUserDataState)
    const [componentTxs, setComponentTxs] = useState({})

    const isMobile = useIsMobile()

    useEffect(() => {
        GApage('pools')
    }, [])

    useEffect(() => {
        console.log(
            'Add Liquidity: called checkAndUpdateComponentTransactions ',
            componentTxs
        )
        checkAndUpdateComponentTransactions(
            transactionsStatus,
            componentTxs,
            setAddLiquidityInProgress
        )
    }, [transactionsStatus, componentTxs])

    useEffect(() => {}, [tuds, pools])

    useEffect(() => {
        if (token1 && token2) {
            let poolId = getPoolIdFromTokens(token1, token2)
            setActivePoolId(poolId)

            // follow the leader
            if (leadingToken <= 1) {
                // cannot be 0, but just for safety
                if (poolId > 0) {
                    let amount = calcOtherTokenRequiredAmountForLiquidity(
                        token1,
                        token2,
                        token1Amount
                    )
                    //console.log('Provide Liquidity amount: ', amount)
                    if (amount !== undefined) setToken2Amount(amount)
                } else {
                    setToken2Amount(0)
                }
            } else {
                if (poolId > 0) {
                    let amount = calcOtherTokenRequiredAmountForLiquidity(
                        token2,
                        token1,
                        token2Amount
                    )
                    if (amount !== undefined) setToken1Amount(amount)
                } else {
                    setToken1Amount(0)
                }
            }
        }
    }, [token1, token2, token1Amount, token2Amount, leadingToken, pools])

    useEffect(() => {
        if (activePoolId > 0) forceRefreshPool(activePoolId)
    }, [activePoolId])

    const updateTokensIntervalCallback = async () => {
        // Allow periodic update only if both tokens are selected
        if (activePoolId > 0) {
            updatePoolDynamicData(activePoolId)
        } else {
            if (token1) updateTokenDynamicData(token1)
            if (token2) updateTokenDynamicData(token2)
        }
    }

    // timer to update token1 user data (allowance, amount)
    // and selected pool data
    useInterval(updateTokensIntervalCallback, 15000)

    const addMoreCallback = (pool_id) => {
        console.log('addMoreCallback: pool=', pool_id)
        let pool = pools.get(pool_id)
        if (pool) {
            setToken1(pool.token1)
            setToken2(pool.token2)
            setToken2Amount(undefined)
            setToken1Amount(undefined)
            let element = document.getElementById('scroll_to_top_here')
            element.scrollIntoView({
                behavior: 'smooth',
                block: 'end',
                inline: 'nearest'
            })
        }
    }

    const handleToken1Select = (event) => {
        let token_address = event.target.value
        setToken1(token_address)
        updateTokenDynamicData(token_address)

        GAbutton('pool-token1')
    }

    const handleToken2Select = (event) => {
        let token_address = event.target.value
        setToken2(token_address)
        updateTokenDynamicData(token_address)

        GAbutton('pool-token2')
    }

    // Fixme: a perfect dup of the function in swap - consider extracting to util service module
    const handleTokenAmountChange = (token, amount) => {
        // fixme: consider limit decimals input according to token decimals
        const regex = /^(([0-9]+[.]?[0-9]*)|([.][0-9]+))$/

        if (token === token1 && (amount === '' || regex.test(amount))) {
            setToken1Amount(amount)
            setLeadingToken(1)
        }
        if (token === token2 && (amount === '' || regex.test(amount))) {
            setToken2Amount(amount)
            setLeadingToken(2)
        }
    }

    const handleAddLiquidity = async () => {
        if (
            (token1 === TST_TOKEN_ADDRESS && token1Amount > TST_TOKEN_MAX_ALLOWED) ||
            (token2 === TST_TOKEN_ADDRESS && token2Amount > TST_TOKEN_MAX_ALLOWED)
        ) {
            addToastMsg('TST token is limited to ' + TST_TOKEN_MAX_ALLOWED, 'error')
            GAbutton('add_liquidity-tst-over-max-allowed')
            return
        }

        if (!isPoolUpToDate(activePoolId)) {
            GAbutton('add_liquidity-not-up-to-date')
            return
        }

        setAddLiquidityInProgress(true)
        GAbutton('add-liquidity')
        let slippage = parseFloat(getRecoil(addLiquiditySlippage)) / 100
        let minAmount1Out = BigNumber(token1Amount)
            .times(BigNumber(1).minus(BigNumber(slippage)))
            .toString(10)
        let minAmount2Out = BigNumber(token2Amount)
            .times(BigNumber(1).minus(BigNumber(slippage)))
            .toString(10)
        console.log('Add Liquidity slippage: ', slippage)
        console.log(
            'Add Liquidity minAmountOut: ',
            minAmount1Out,
            float_to_decimal_int(token1, minAmount1Out)
        )

        try {
            let label = 'add_liquidity'
            let token1_obj = tokens.get(token1)
            let token2_obj = tokens.get(token2)
            if (token1_obj && token2_obj) {
                label =
                    'add_liquidity:' +
                    activePoolId +
                    ':' +
                    token1_obj.symbol +
                    '=' +
                    token1Amount +
                    ':' +
                    token2_obj.symbol +
                    '=' +
                    token2Amount
            }
            let tx = await add_liquidity(
                token1,
                float_to_decimal_int(token1, token1Amount),
                float_to_decimal_int(token1, minAmount1Out),
                token2,
                float_to_decimal_int(token2, token2Amount),
                float_to_decimal_int(token2, minAmount2Out),
                activePoolId,
                label
            )

            if (!tx) throw Error('Cannot perform transaction')

            setComponentTxs((prevState) => ({
                ...prevState,
                [tx.transaction_hash]: 'new'
            }))
            console.log(
                'Add Liquidity: Setting component transaction ',
                typeof tx.transaction_hash,
                componentTxs
            )
        } catch (e) {
            if (e.message.includes('User abort')) {
                addToastMsg('Add Liquidity Request: User abort', 'error')
            }
            setAddLiquidityInProgress(false)
        }
    }

    const addLiquidityButton = () => {
        let token1SufficientBalance = isSufficientBalanceInWallet(token1, token1Amount)
        let token2SufficientBalance = isSufficientBalanceInWallet(token2, token2Amount)

        if (
            token1 &&
            token2 &&
            token1Amount > 0 &&
            token2Amount > 0 &&
            token1SufficientBalance === WALLET_SUFFICIENT_BALANCE.TRUE &&
            token2SufficientBalance === WALLET_SUFFICIENT_BALANCE.TRUE &&
            activePoolId !== 0
        ) {
            // PoolId is valid - display ADD LIQUIDITY button
            return (
                <StyledLoadingButton
                    fullWidth={true}
                    disabled={false}
                    loading={addLiquidityInProgress}
                    loadingPosition='start'
                    startIcon={<MonetizationOnIcon />}
                    onClick={handleAddLiquidity}>
                    <StyledButtonTypography sx={{ fontSize: isMobile ? 12 : 20 }}>
                        {addLiquidityInProgress ? 'ADDING LIQUIDITY' : 'ADD LIQUIDITY'}
                    </StyledButtonTypography>
                </StyledLoadingButton>
            )
        }

        if (
            connectedAddress &&
            token1 &&
            token2 &&
            token1Amount > 0 &&
            token2Amount > 0 &&
            (token1SufficientBalance !== WALLET_SUFFICIENT_BALANCE.TRUE ||
                token2SufficientBalance !== WALLET_SUFFICIENT_BALANCE.TRUE)
        ) {
            // Amount exceed wallet balance
            return (
                <StyledLoadingButton
                    fullWidth={true}
                    disabled={true}
                    loading={
                        token1SufficientBalance === WALLET_SUFFICIENT_BALANCE.UNKNOWN ||
                        token2SufficientBalance === WALLET_SUFFICIENT_BALANCE.UNKNOWN
                    }
                    loadingPosition='start'
                    startIcon={<AccountBalanceWalletIcon />}>
                    <StyledButtonTypography sx={{ fontSize: isMobile ? 12 : 20 }}>
                        {token1SufficientBalance === WALLET_SUFFICIENT_BALANCE.FALSE ||
                        token2SufficientBalance === WALLET_SUFFICIENT_BALANCE.FALSE
                            ? 'AMOUNT EXCEEDS WALLET BALANCE'
                            : 'CHECKING WALLET BALANCE'}
                    </StyledButtonTypography>
                </StyledLoadingButton>
            )
        }

        if (token1 && token2 && activePoolId === 0) {
            // No pool for token pair selected
            return (
                <StyledLoadingButton
                    color='primary'
                    variant='contained'
                    fullWidth={true}
                    disabled={true}>
                    <StyledButtonTypography sx={{ fontSize: isMobile ? 12 : 20 }}>
                        NOT A VALID POOL
                    </StyledButtonTypography>
                </StyledLoadingButton>
            )
        }

        if (connectedAddress) {
            // Wallet is connected
            let instruction = ''
            if (!token1 && !token2) instruction = 'SELECT TOKENS'
            if ((!token1 && token2) || (token1 && !token2)) instruction = 'SELECT TOKEN'
            if (token1 && token2 && (!token1Amount || !token2Amount))
                instruction = 'ENTER AMOUNT'

            if (instruction !== '')
                return (
                    <StyledLoadingButton
                        color='secondary'
                        variant='contained'
                        fullWidth={true}
                        disabled={true}>
                        <StyledButtonTypography sx={{ fontSize: isMobile ? 12 : 20 }}>
                            {instruction}
                        </StyledButtonTypography>
                    </StyledLoadingButton>
                )
        }

        return <div></div>
    }

    const priceAndShare = () => {
        let pool = pools.get(activePoolId)

        if (!token1Amount || !token2Amount || !activePoolId || !pool) return <div></div>

        let symbol1 = tokens.get(token1).symbol
        let symbol2 = tokens.get(token2).symbol

        let priceForToken1 = getPoolPriceForTokenA(activePoolId, token2, token1)
        let priceForToken2 = getPoolPriceForTokenA(activePoolId, token1, token2)

        // Normalize reserves amounts and get expected share in the pool
        let pool_share =
            pool.token1 === token1
                ? BigNumber(token1Amount).div(
                      BigNumber(token1Amount).plus(
                          BigNumber(decimal_int_to_float(pool.token1, pool.token1Amount))
                      )
                  )
                : BigNumber(token1Amount).div(
                      BigNumber(token1Amount).plus(
                          BigNumber(decimal_int_to_float(pool.token2, pool.token2Amount))
                      )
                  )

        return (
            <>
                <Grid>
                    <Card sx={{ p: 3, boxShadow: 2, mt: 1, backgroundColor: '#201e43' }}>
                        <CardContent sx={{ mt: -3.5 }}>
                            <Typography
                                sx={{
                                    fontSize: 18,
                                    fontWeight: 'normal',
                                    fontColor: '#fff',
                                    mb: -1
                                }}>
                                Pool Prices and Share
                            </Typography>
                        </CardContent>

                        <Divider
                            variant='middle'
                            sx={{
                                borderBottomWidth: 1,
                                backgroundColor: 'rgba(255, 255, 255, 0.2)'
                            }}
                        />

                        <Card
                            sx={{
                                mb: -2.5,
                                mt: -0.5,
                                backgroundColor: 'transparent',
                                boxShadow: 0
                            }}>
                            <Grid item container alignItems='center' spacing={2}>
                                <Grid item xs={4}>
                                    <Typography
                                        sx={{
                                            textAlign: 'center',
                                            lineHeight: 2,
                                            mt: 1,
                                            fontWeight: 'bold'
                                        }}>
                                        {numFormat(priceForToken1)}
                                    </Typography>
                                    <Typography
                                        sx={{
                                            textAlign: 'center',
                                            fontSize: 12,
                                            mb: 1,
                                            color: '#6f6e8f'
                                        }}>
                                        {symbol1} for {symbol2}
                                    </Typography>
                                </Grid>
                                <Grid item xs={4}>
                                    <Typography
                                        sx={{
                                            textAlign: 'center',
                                            lineHeight: 2,
                                            mt: 1,
                                            fontWeight: 'bold'
                                        }}>
                                        {numFormat(priceForToken2)}
                                    </Typography>
                                    <Typography
                                        sx={{
                                            textAlign: 'center',
                                            fontSize: 12,
                                            mb: 1,
                                            color: '#6f6e8f'
                                        }}>
                                        {symbol2} for {symbol1}
                                    </Typography>
                                </Grid>
                                <Grid item xs={4}>
                                    <Typography
                                        sx={{
                                            textAlign: 'center',
                                            lineHeight: 2,
                                            mt: 1,
                                            fontWeight: 'bold'
                                        }}>
                                        {numFormat(pool_share, true)}%
                                    </Typography>
                                    <Typography
                                        sx={{
                                            textAlign: 'center',
                                            fontSize: 12,
                                            mb: 1,
                                            color: '#6f6e8f'
                                        }}>
                                        Share of Pool
                                    </Typography>
                                </Grid>
                            </Grid>
                        </Card>
                    </Card>
                </Grid>
            </>
        )
    }

    return (
        <div>
            <Box
                id='scroll_to_top_here'
                sx={{
                    p: isMobile ? 1.5 : 3,
                    border: isMobile ? 'none' : '1px solid',
                    borderColor: 'rgba(255, 255, 255, 0.27)',
                    mt: 0,
                    mb: 4,
                    boxShadow: 40,
                    borderRadius: isMobile ? 1.5 : 2,
                    backgroundColor: '#0d0c1e'
                }}>
                <Grid container sx={{ mb: 2 }}>
                    <BoxHeader name='Add Liquidity' />
                </Grid>

                <Card
                    sx={{
                        p: isMobile ? 1 : 2,
                        boxShadow: 2,
                        mb: 1,
                        backgroundColor: '#201e43'
                    }}>
                    <Grid container spacing={isMobile ? 0 : 2}>
                        <Grid item xs={6} sm={5}>
                            <TokenSelector
                                token={token1}
                                tokenStr={'Token 1'}
                                otherToken={token2}
                                tokens={tokens}
                                handleTokenSelect={handleToken1Select}
                                showMaxButton={true}
                                handleTokenAmountChange={handleTokenAmountChange}
                            />
                        </Grid>

                        <Grid item xs={6} sm={7}>
                            <FormControl fullWidth>
                                <TextField
                                    helperText='Amount to provide'
                                    id='token1-amount'
                                    inputProps={{
                                        variant: 'outlined',
                                        placeholder: '0.00',
                                        type: 'string',
                                        autoComplete: 'off',
                                        style: { fontSize: isMobile ? 13 : 24 },
                                        'aria-labelledby': 'input-amount1'
                                    }}
                                    sx={{
                                        input: { background: '#0d0c1e', borderRadius: 1 },
                                        '&:hover': {
                                            '&& fieldset': {
                                                border: '0px solid green',
                                                color: 'white'
                                            }
                                        },
                                        '.MuiOutlinedInput-notchedOutline': {
                                            border: '0px'
                                        }
                                    }}
                                    value={token1Amount ? token1Amount : ''}
                                    onChange={(e) =>
                                        handleTokenAmountChange(token1, e.target.value)
                                    }
                                    error={
                                        isSufficientBalanceInWallet(
                                            token1,
                                            token1Amount
                                        ) === WALLET_SUFFICIENT_BALANCE.FALSE
                                            ? true
                                            : false
                                    }
                                    disabled={token1 === '' ? true : false}
                                />
                            </FormControl>
                        </Grid>
                    </Grid>
                </Card>

                <Card
                    sx={{
                        p: isMobile ? 1 : 2,
                        mt: 0,
                        boxShadow: 2,
                        backgroundColor: '#201e43'
                    }}>
                    <Grid container spacing={isMobile ? 0 : 2}>
                        <Grid item xs={6} sm={5}>
                            <TokenSelector
                                token={token2}
                                tokenStr={'Token 2'}
                                otherToken={token1}
                                tokens={tokens}
                                handleTokenSelect={handleToken2Select}
                                showMaxButton={true}
                                handleTokenAmountChange={handleTokenAmountChange}
                            />
                        </Grid>

                        <Grid item xs={6} sm={7}>
                            <FormControl fullWidth>
                                <TextField
                                    helperText='Amount to provide'
                                    id='token2-amount'
                                    inputProps={{
                                        variant: 'outlined',
                                        placeholder: '0.00',
                                        type: 'string',
                                        autoComplete: 'off',
                                        style: { fontSize: isMobile ? 13 : 24 },
                                        'aria-labelledby': 'input-amount2'
                                    }}
                                    sx={{
                                        input: { background: '#0d0c1e', borderRadius: 1 },
                                        '&:hover': {
                                            '&& fieldset': {
                                                border: '0px solid green',
                                                color: 'white'
                                            }
                                        },
                                        '.MuiOutlinedInput-notchedOutline': {
                                            border: '0px'
                                        }
                                    }}
                                    value={token2Amount ? token2Amount : ''}
                                    onChange={(e) =>
                                        handleTokenAmountChange(token2, e.target.value)
                                    }
                                    error={
                                        isSufficientBalanceInWallet(
                                            token2,
                                            token2Amount
                                        ) === WALLET_SUFFICIENT_BALANCE.FALSE
                                            ? true
                                            : false
                                    }
                                    disabled={token2 === '' ? true : false}
                                />
                            </FormControl>
                        </Grid>
                    </Grid>
                </Card>

                {priceAndShare()}

                <Card
                    sx={{ mb: -1, mt: 2, backgroundColor: 'transparent', boxShadow: 0 }}>
                    <ConnectWallet secondary='true' />

                    {addLiquidityButton()}
                </Card>
            </Box>

            {/* User's position boxes */}
            <Positions addMoreCallback={addMoreCallback} />
        </div>
    )
}

export default Provide
