import React from 'react'

import { Grid, Button, Typography } from '@mui/material'

import { numFormat, TST_TOKEN_ADDRESS } from '../services/amm'
import useIsMobile from '../hooks/useIsMobile'

function Balance(props) {
    const isMobile = useIsMobile()
    let token = props.token
    let isTstToken = token === TST_TOKEN_ADDRESS
    let setMaxAmountHandler = props.setMaxAmountHandler
    let walletAmount = props.amount

    let showMaxButton = props.showMaxButton && walletAmount > 0 && !isMobile

    let maxAmount = isTstToken ? 1000 : walletAmount
    let showFaucet =
        !isMobile &&
        isTstToken &&
        (walletAmount === 0 || walletAmount === undefined || walletAmount === '0')

    if (walletAmount === undefined || walletAmount === '') return <div></div>

    if (maxAmount > walletAmount) maxAmount = walletAmount

    return (
        <div>
            <Grid container>
                {showFaucet && (
                    <Button
                        size='small'
                        sx={{ minWidth: 0, ml: 2, mt: 0.3 }}
                        onClick={() =>
                            window.open('https://braavos.app/faucet', '_blank')
                        }>
                        <Typography color='#4c68ee' fontSize={13}>
                            mint TST tokens
                        </Typography>
                    </Button>
                )}

                <Typography
                    fontSize={13}
                    sx={{
                        ml: isMobile ? 0.5 : 2,
                        mt: 0.5,
                        maxWidth: '90%',
                        whiteSpace: 'nowrap',
                        overflow: 'hidden',
                        textOverflow: 'ellipsis'
                    }}>
                    {!showFaucet && 'Balance: ' + numFormat(walletAmount)}
                </Typography>

                {showMaxButton && (
                    <Button
                        size='small'
                        sx={{ minWidth: 0, ml: 0.05, mt: -0.05 }}
                        onClick={() => setMaxAmountHandler(token, maxAmount)}>
                        <Typography color='#4c68ee' fontSize={13}>
                            max
                        </Typography>
                    </Button>
                )}
            </Grid>
        </div>
    )
}

export default Balance
