import React, { useEffect } from 'react'
import { useRecoilState } from 'recoil'
import { BigNumber } from 'bignumber.js'

import LiquidityPool from './LiquidityPool'
import { lpBalanceState, numOfPoolsState, walletAddressState } from '../atoms'
import { updatePoolData, updateUserLPBalance } from '../services/amm'
import { useInterval } from 'usehooks-ts'

function Positions(props) {
    const [lpBalance] = useRecoilState(lpBalanceState)
    const [numPools] = useRecoilState(numOfPoolsState)
    const [connectedAddress] = useRecoilState(walletAddressState)

    useEffect(() => {
        updateUserLPBalance()
    }, [connectedAddress, numPools])

    const updatePoolsIntervalCallback = async () => {
        // Allow periodic update for pools with positions
        for (let i = 1; i <= numPools; i++) {
            const balance = lpBalance.get(i) || 0
            if (!BigNumber(balance).isZero()) {
                updatePoolData(i)
            }
        }
    }

    // timer to update pools where we have open positions
    useInterval(updatePoolsIntervalCallback, 60000)

    const allPools = () => {
        let r = []
        for (let i = 1; i <= numPools; i++) {
            const balance = lpBalance.get(i) || 0
            if (!BigNumber(balance).isZero()) {
                r.push(
                    <LiquidityPool
                        key={i}
                        poolId={i}
                        balance={balance}
                        addMoreCallback={props.addMoreCallback}
                    />
                )
                //YS: don't call this from UI thread. updatePoolDynamicData(poolId)
            }
        }
        return r
    }

    return <div>{allPools()}</div>
}

export default Positions
