import React, { useState, useEffect } from 'react'
import { useRecoilState } from 'recoil'
import { getRecoil } from 'recoil-nexus'
import { BigNumber } from 'bignumber.js'

import {
    FormControl,
    TextField,
    Grid,
    Box,
    Card,
    CardActionArea,
    Typography,
    IconButton
} from '@mui/material'
import DoneIcon from '@mui/icons-material/Done'
import SwapHorizIcon from '@mui/icons-material/SwapHoriz'
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet'
import SwitchIcon from '../images/icons/switch_arrows_bgd_0d0c1e.png'

import { GApage, GAbutton } from '../services/ga.js'

import {
    tokensState,
    swapSlippage,
    walletAddressState,
    transactionsStatusSelector,
    tokensUserDataState,
    poolsState
} from '../atoms'

import {
    updateTokenDynamicData,
    updatePoolDynamicData,
    swap,
    numFormat,
    addToastMsg,
    getPoolPriceForTokenA,
    getPoolIdFromTokens,
    calcOtherTokenAmount,
    float_to_decimal_int,
    isSufficientBalanceInWallet,
    isInsufficientBalanceInPool,
    checkAndUpdateComponentTransactions,
    WALLET_SUFFICIENT_BALANCE,
    isPoolUpToDate,
    forceRefreshPool,
    TST_TOKEN_ADDRESS,
    TST_TOKEN_MAX_ALLOWED
} from '../services/amm'

import { addToken } from '../services/wallet.service'

import { useInterval } from 'usehooks-ts'

import BoxHeader from './BoxHeader.js'
import TokenSelector from './TokenSelector.js'
import ConnectWallet from './ConnectWallet.js'
import { StyledLoadingButton, StyledButtonTypography } from './StyleCustomizations'
import useIsMobile from '../hooks/useIsMobile'

function Swap() {
    const [activePoolId, setActivePoolId] = useState(0)
    const [token1, setToken1] = useState('')
    const [token1Amount, setToken1Amount] = useState('')
    const [token2, setToken2] = useState('')
    const [token2Amount, setToken2Amount] = useState('')
    const [leadingToken, setLeadingToken] = useState(0) // 0 is no token, 1/2 is the last updated token
    const [swapInProgress, setSwapInProgress] = useState(false)
    const [tokens] = useRecoilState(tokensState)
    const [tuds] = useRecoilState(tokensUserDataState)
    const [pools] = useRecoilState(poolsState)

    const [connectedAddress] = useRecoilState(walletAddressState)
    const [transactionsStatus] = useRecoilState(transactionsStatusSelector('swap'))
    const [componentTxs, setComponentTxs] = useState({})

    const [priceDirection, setPriceDirection] = useState(0)
    const [slippage] = useRecoilState(swapSlippage)
    const [displayAddTokenToWallet, setDisplayAddTokenToWallet] = useState(false)
    const [tokenAddedToWallet, setTokenAddedToWallet] = useState(false)
    const isMobile = useIsMobile()

    useEffect(() => {
        GApage('swap')
    }, [])

    useEffect(() => {
        checkAndUpdateComponentTransactions(
            transactionsStatus,
            componentTxs,
            setSwapInProgress
        )
    }, [transactionsStatus, componentTxs])

    useEffect(() => {
        if (swapInProgress === false) {
            setDisplayAddTokenToWallet(false)
        }
    }, [swapInProgress])

    useEffect(() => {}, [tuds, pools])

    const updateTokensIntervalCallback = async () => {
        // Allow periodic update only if both tokens are selected
        if (activePoolId > 0) {
            updatePoolDynamicData(activePoolId)
        } else {
            if (token1) updateTokenDynamicData(token1)
            if (token2) updateTokenDynamicData(token2)
        }
    }

    // Timer to update active pool and tokens user data (allowance, amount)
    useInterval(updateTokensIntervalCallback, 30000)

    const handleToken1Select = (event) => {
        let token_address = event.target.value
        setToken1(token_address)
        updateTokenDynamicData(token_address)

        GAbutton('swap-token1')
    }

    const handleToken2Select = (event) => {
        let token_address = event.target.value
        setToken2(token_address)
        updateTokenDynamicData(token_address)
        setTokenAddedToWallet(token_address === token2)
        setDisplayAddTokenToWallet(false)

        GAbutton('swap-token2')
    }

    const handleTokenAmountChange = (token, amount) => {
        // fixme: consider limit decimals input according to token decimals
        const regex = /^(([0-9]+[.]?[0-9]*)|([.][0-9]+))$/

        if (token === token1 && (amount === '' || regex.test(amount))) {
            setToken1Amount(amount)
            setLeadingToken(1)
        }
        if (token === token2 && (amount === '' || regex.test(amount))) {
            setToken2Amount(amount)
            setLeadingToken(2)
        }
    }

    useEffect(() => {
        if (token1 && token2) {
            let poolId = getPoolIdFromTokens(token1, token2)
            setActivePoolId(poolId)

            // follow the leader
            if (leadingToken <= 1) {
                // cannot be 0, but just for safety
                if (poolId > 0) {
                    // calc token2 amount
                    let amount = calcOtherTokenAmount(token1, token2, token1Amount, 1)
                    console.log('calcOtherTokenAmount: (token1 leading) ', amount)
                    if (amount !== undefined && amount !== token2Amount) {
                        setToken2Amount(amount)
                    }
                } else {
                    setToken2Amount(0)
                }
            } else {
                if (poolId > 0) {
                    // calc token2 amount
                    let amount = calcOtherTokenAmount(token2, token1, token2Amount, 2)
                    console.log('calcOtherTokenAmount: (token2 leading) ', amount)
                    if (amount !== undefined && amount < 0) amount = 0
                    else if (amount !== undefined && amount !== token1Amount) {
                        setToken1Amount(amount)
                    }
                } else {
                    setToken1Amount(0)
                }
            }
        } else {
            // follow the leader
            if (leadingToken <= 1) {
                // cannot be 0, but just for safety
                setToken2Amount('')
            } else {
                setToken1Amount('')
            }
        }
    }, [token1, token2, token1Amount, token2Amount, leadingToken, pools])

    useEffect(() => {
        if (activePoolId > 0) forceRefreshPool(activePoolId)
    }, [activePoolId])

    const calcToken2MinAmount = () => {
        let slippage = parseFloat(getRecoil(swapSlippage)) / 100
        return BigNumber(token2Amount).times(BigNumber(1).minus(BigNumber(slippage)))
    }

    const handleSwap = async () => {
        if (token1 === TST_TOKEN_ADDRESS && token1Amount > TST_TOKEN_MAX_ALLOWED) {
            addToastMsg('TST token swap is limited to ' + TST_TOKEN_MAX_ALLOWED, 'error')
            GAbutton('swap-tst-over-max-allowed')
            return
        }

        if (!isPoolUpToDate(activePoolId, false)) {
            // false since we don't care about pool total shares
            GAbutton('swap-not-up-to-date')
            return
        }

        setSwapInProgress(true)
        GAbutton('swap')

        let minAmountOut = calcToken2MinAmount()
        try {
            let label = 'swap'
            let token_obj = tokens.get(token1)
            if (token_obj) {
                label =
                    'swap:' +
                    activePoolId +
                    ':from=' +
                    token_obj.symbol +
                    ':amount=' +
                    token1Amount
            }
            let tx = await swap(
                activePoolId,
                token1,
                float_to_decimal_int(token1, token1Amount),
                float_to_decimal_int(token2, minAmountOut),
                label
            )

            if (!tx) throw Error('Cannot perform transaction')

            setComponentTxs((prevState) => ({
                ...prevState,
                [tx.transaction_hash]: 'new'
            }))

            setDisplayAddTokenToWallet(!tokenAddedToWallet)
        } catch (e) {
            if (e.message.includes('User abort')) {
                addToastMsg('Swap Request: User Aborted', 'error')
            }
            setSwapInProgress(false)
        }
    }

    const swapButton = () => {
        let token1SufficientBalance = isSufficientBalanceInWallet(token1, token1Amount)
        let token2InsufficientPoolBalance = isInsufficientBalanceInPool(
            activePoolId,
            token2,
            token2Amount
        )

        if (
            token2 &&
            token1Amount > 0 &&
            activePoolId !== 0 &&
            token1SufficientBalance === WALLET_SUFFICIENT_BALANCE.TRUE &&
            !token2InsufficientPoolBalance
        ) {
            // Display SWAP button
            return (
                <StyledLoadingButton
                    fullWidth={true}
                    disabled={false}
                    loading={swapInProgress}
                    loadingPosition='start'
                    startIcon={<SwapHorizIcon fontSize='large' />}
                    onClick={handleSwap}>
                    <StyledButtonTypography sx={{ fontSize: isMobile ? 12 : 20 }}>
                        {swapInProgress ? 'SWAPPING' : 'SWAP'}
                    </StyledButtonTypography>
                </StyledLoadingButton>
            )
        }

        if (
            connectedAddress &&
            token1Amount > 0 &&
            token1SufficientBalance !== WALLET_SUFFICIENT_BALANCE.TRUE &&
            !token2InsufficientPoolBalance
        ) {
            // Amount either exceeds wallet balance, or wallet balance is unknown yet
            return (
                <StyledLoadingButton
                    fullWidth={true}
                    disabled={true}
                    loading={
                        token1SufficientBalance === WALLET_SUFFICIENT_BALANCE.UNKNOWN
                    }
                    loadingPosition='start'
                    startIcon={<AccountBalanceWalletIcon />}>
                    <StyledButtonTypography sx={{ fontSize: isMobile ? 12 : 20 }}>
                        {token1SufficientBalance === WALLET_SUFFICIENT_BALANCE.FALSE
                            ? 'AMOUNT EXCEEDS WALLET BALANCE'
                            : 'CHECKING WALLET BALANCE'}
                    </StyledButtonTypography>
                </StyledLoadingButton>
            )
        }

        if (token1 && token2 && activePoolId === 0) {
            // No pool for token pair selected
            return (
                <StyledLoadingButton disabled={true}>
                    <StyledButtonTypography sx={{ fontSize: isMobile ? 12 : 20 }}>
                        NOT A VALID POOL
                    </StyledButtonTypography>
                </StyledLoadingButton>
            )
        }

        if (
            token1 &&
            token2 &&
            activePoolId &&
            token2Amount &&
            token2InsufficientPoolBalance
        ) {
            return (
                <StyledLoadingButton disabled={true}>
                    <StyledButtonTypography sx={{ fontSize: isMobile ? 12 : 20 }}>
                        INSUFFICIENT LIQUIDITY IN POOL
                    </StyledButtonTypography>
                </StyledLoadingButton>
            )
        }

        if (connectedAddress) {
            // Wallet is connected
            let instruction = ''
            if (!token1 && !token2) instruction = 'SELECT TOKENS'
            if ((!token1 && token2) || (token1 && !token2)) instruction = 'SELECT TOKEN'
            if (token1 && token2 && (!token1Amount || !token2Amount))
                instruction = 'ENTER AMOUNT'

            if (instruction !== '')
                return (
                    <StyledLoadingButton disabled={true}>
                        <StyledButtonTypography sx={{ fontSize: isMobile ? 12 : 20 }}>
                            {instruction}
                        </StyledButtonTypography>
                    </StyledLoadingButton>
                )
        }

        return <div></div>
    }

    const switchTokensAndAmounts = () => {
        GAbutton('swap-switchtokens')

        let tmpToken = token1
        let tmpAmount = token1Amount
        setToken1(token2)
        setToken2(tmpToken)
        setToken1Amount(token2Amount)
        setToken2Amount(tmpAmount)
        setLeadingToken(leadingToken === 1 ? 2 : 1)
        setTokenAddedToWallet(false)
        setDisplayAddTokenToWallet(false)
    }

    const isToken1InputDisabled = () => {
        return token1 === '' ? true : false
    }
    const isToken2InputDisabled = () => {
        return token2 === '' ? true : false
    }

    const addTokenToWallet = () => {
        const handleAddTokenClick = () => {
            try {
                addToken(token2)
                setTokenAddedToWallet(true)
            } catch (e) {
                console.log('Error adding token to wallet: ' + e.message)
                if (e.message.includes('User abort')) {
                    addToastMsg('Error adding token to wallet: User Aborted', 'error')
                }
            }
        }

        //console.log('AddTokenToWallet: ', tokens, token2, displayAddTokenToWallet, tokenAddedToWallet)
        if (tokens && token2 && displayAddTokenToWallet) {
            return (
                <StyledLoadingButton
                    fullWidth
                    sx={{ mb: 1 }}
                    onClick={handleAddTokenClick}
                    disabled={tokenAddedToWallet}>
                    {tokenAddedToWallet && (
                        <DoneIcon color='success' sx={{ mb: 0.2, mr: 0.5 }} />
                    )}

                    <Typography
                        color='rgba(240, 240, 240, 0.9)'
                        fontSize={16}
                        sx={{ textTransform: 'none', alignItems: 'center' }}>
                        {tokenAddedToWallet
                            ? 'ADDED ' + tokens.get(token2).symbol
                            : 'ADD ' + tokens.get(token2).symbol + ' TO WALLET'}
                    </Typography>
                </StyledLoadingButton>
            )
        }
    }

    const priceAndSlippage = () => {
        if (
            token1 &&
            token2 &&
            token1Amount &&
            !BigNumber(token1Amount).isZero() &&
            token2Amount &&
            !BigNumber(token2Amount).isZero()
        ) {
            // currentPrice = How much token2 for 1 token1
            let currentPrice = getPoolPriceForTokenA(activePoolId, token1, token2)
            console.log('CurrentPrice: ', currentPrice)
            let minAmount = calcToken2MinAmount()
            let token1Symbol = tokens.get(token1).symbol
            let token2Symbol = tokens.get(token2).symbol
            let floatSlippage = parseFloat(slippage)

            // get token amounts without fee
            let amount1 = token1Amount
            let amount2 = token2Amount
            if (leadingToken === 1)
                // get otherTokenAmount without fee
                amount2 = calcOtherTokenAmount(token1, token2, token1Amount, 1, false)
            // leadingToken === 2
            else amount1 = calcOtherTokenAmount(token2, token1, token2Amount, 2, false)

            // transactionPrice = How much token2 for 1 token1
            let transactionPrice = BigNumber(amount2).div(BigNumber(amount1))
            let transactionPriceInv = BigNumber(amount1).div(BigNumber(amount2))

            let priceImpact = BigNumber(1).minus(
                BigNumber(transactionPrice).div(BigNumber(currentPrice))
            )
            //console.log('Calc PriceImpact: ', priceImpact.toString(), transactionPrice.toString(), currentPrice.toString())

            let backgroundColor = priceImpact.gt(0.05)
                ? 'rgba(229, 40, 28, 0.45)'
                : priceImpact.gt(0.03)
                ? 'rgba(255, 120, 31, 0.45)'
                : '#201e43'

            let textColor = priceImpact.gt(0.03)
                ? '#rgba(240, 240, 240, 0.45)'
                : '#6f6e8f'

            let displayPrice1 = (
                <>
                    1 {token1Symbol} = {numFormat(transactionPrice)} {token2Symbol}
                </>
            )
            let displayPrice2 = (
                <>
                    1 {token2Symbol} = {numFormat(transactionPriceInv)} {token1Symbol}
                </>
            )
            //console.log('Price: ', displayPrice1, displayPrice2, priceDirection, transactionPrice.toString(10))

            return (
                <Grid>
                    <Card
                        sx={{
                            mb: 1,
                            mt: 1,
                            boxShadow: 2,
                            backgroundColor: '#201e43'
                        }}>
                        <CardActionArea
                            disableRipple
                            sx={{
                                p: 1
                                // width: 'fit-content',
                            }}
                            onClick={() => setPriceDirection(!priceDirection)}>
                            <Typography
                                fontSize={isMobile ? 12 : 16}
                                fontWeight='bold'
                                sx={{
                                    ml: isMobile ? 0.5 : 2,
                                    mr: 1 /*, textDecoration: "underline" */
                                }}>
                                {priceDirection ? displayPrice2 : displayPrice1}
                            </Typography>
                        </CardActionArea>
                    </Card>

                    <Card
                        sx={{
                            px: 1,
                            py: 1,
                            mb: 1,
                            mt: 1,
                            boxShadow: 2,
                            backgroundColor
                        }}>
                        <Grid container>
                            <Grid>
                                <Typography
                                    fontSize={isMobile ? 12 : 16}
                                    fontWeight='medium'
                                    sx={{
                                        ml: isMobile ? 0.5 : 2,
                                        mr: 1,
                                        color: textColor
                                    }}>
                                    {isMobile ? 'Min' : 'Minimum amount'} received
                                    (slippage=
                                    {floatSlippage.toFixed(2)}
                                    %):
                                </Typography>
                            </Grid>
                            <Grid sx={{ maxWidth: isMobile ? '35%' : '100%' }}>
                                <Typography
                                    fontWeight='bold'
                                    fontSize={isMobile ? 12 : 16}
                                    sx={{
                                        maxWidth: isMobile ? 'none' : '100%',
                                        whiteSpace: 'nowrap',
                                        overflow: 'hidden',
                                        textOverflow: 'ellipsis'
                                    }}>
                                    {numFormat(minAmount)}
                                </Typography>
                            </Grid>
                        </Grid>

                        <Grid container>
                            <Grid>
                                <Typography
                                    fontSize={isMobile ? 12 : 16}
                                    fontWeight='medium'
                                    sx={{
                                        ml: isMobile ? 0.5 : 2,
                                        mr: 1,
                                        color: textColor
                                    }}>
                                    Price impact:
                                </Typography>
                            </Grid>
                            <Grid>
                                <Typography
                                    fontWeight='bold'
                                    fontSize={isMobile ? 12 : 16}>
                                    {numFormat(priceImpact, true)}%
                                </Typography>
                            </Grid>
                        </Grid>
                    </Card>
                </Grid>
            )
        }
        return <div></div>
    }

    return (
        <div>
            <Box
                sx={{
                    p: isMobile ? 1.5 : 3,
                    border: isMobile ? 'none' : '1px solid',
                    borderColor: 'rgba(255, 255, 255, 0.27)',
                    mt: 0,
                    mb: 2,
                    boxShadow: 40,
                    borderRadius: isMobile ? 1.5 : 2,
                    backgroundColor: '#0d0c1e'
                }}>
                <Grid container sx={{ mb: 2 }}>
                    <BoxHeader name='Swap' />
                </Grid>

                <Card
                    sx={{
                        p: isMobile ? 1 : 2,
                        pb: 3,
                        pt: isMobile ? 2.5 : 3.5,
                        boxShadow: 2,
                        mb: -3,
                        backgroundColor: '#201e43'
                    }}>
                    <Grid container spacing={isMobile ? 0 : 2}>
                        <Grid item xs={6} sm={5}>
                            <TokenSelector
                                token={token1}
                                tokenStr={'Token 1'}
                                otherToken={token2}
                                tokens={tokens}
                                handleTokenSelect={handleToken1Select}
                                showMaxButton={true}
                                handleTokenAmountChange={handleTokenAmountChange}
                            />
                        </Grid>
                        <Grid item xs={6} sm={7}>
                            <FormControl fullWidth>
                                <TextField
                                    helperText='Amount to swap'
                                    id='token1-amount'
                                    inputProps={{
                                        placeholder: '0.00',
                                        type: 'string',
                                        autoComplete: 'off',
                                        style: { fontSize: isMobile ? 13 : 24 },
                                        'aria-labelledby': 'input-amount'
                                    }}
                                    sx={{
                                        input: { background: '#0d0c1e', borderRadius: 1 },
                                        '&:hover': {
                                            '&& fieldset': {
                                                border: '0px solid green',
                                                color: 'white'
                                            }
                                        },
                                        '.MuiOutlinedInput-notchedOutline': {
                                            border: '0px'
                                        }
                                    }}
                                    value={token1Amount ? token1Amount : ''}
                                    onChange={(e) =>
                                        handleTokenAmountChange(token1, e.target.value)
                                    }
                                    error={
                                        isSufficientBalanceInWallet(
                                            token1,
                                            token1Amount
                                        ) === WALLET_SUFFICIENT_BALANCE.FALSE
                                            ? true
                                            : false
                                    }
                                    disabled={isToken1InputDisabled()}
                                />
                            </FormControl>
                        </Grid>
                    </Grid>
                </Card>

                {/* The Switch Element */}
                <Grid
                    container
                    direction='row'
                    justifyContent='center'
                    alignItems='center'>
                    <IconButton
                        size='small'
                        disabled={!token1 && !token2}
                        onClick={switchTokensAndAmounts}>
                        <img src={SwitchIcon} alt='' width='50' height='50' />
                    </IconButton>
                </Grid>

                <Card
                    sx={{
                        p: isMobile ? 1 : 2,
                        pb: 3,
                        pt: isMobile ? 2.5 : 3.5,
                        mt: -3.5,
                        boxShadow: 2,
                        backgroundColor: '#201e43'
                    }}>
                    <Grid container spacing={isMobile ? 0 : 2}>
                        <Grid item xs={6} sm={5}>
                            <TokenSelector
                                token={token2}
                                tokenStr={'Token 2'}
                                otherToken={token1}
                                tokens={tokens}
                                handleTokenSelect={handleToken2Select}
                                showMaxButton={false}
                                handleTokenAmountChange={handleTokenAmountChange}
                            />
                        </Grid>

                        <Grid item xs={6} sm={7}>
                            <FormControl fullWidth>
                                <TextField
                                    helperText={
                                        isMobile
                                            ? 'Est. received'
                                            : 'Estimated amount received'
                                    }
                                    id='token2-amount'
                                    inputProps={{
                                        variant: 'outlined',
                                        placeholder: '0.00',
                                        type: 'string',
                                        autoComplete: 'off',
                                        style: { fontSize: isMobile ? 13 : 24 },
                                        'aria-labelledby': 'input-amount'
                                    }}
                                    sx={{
                                        input: { background: '#0d0c1e', borderRadius: 1 },
                                        '&:hover': {
                                            '&& fieldset': {
                                                border: '0px solid green',
                                                color: 'white'
                                            }
                                        },
                                        '.MuiOutlinedInput-notchedOutline': {
                                            border: '0px'
                                        }
                                    }}
                                    value={token2Amount ? token2Amount : ''}
                                    onChange={(e) =>
                                        handleTokenAmountChange(token2, e.target.value)
                                    }
                                    disabled={isToken2InputDisabled()}
                                />
                            </FormControl>
                        </Grid>
                    </Grid>
                </Card>

                {priceAndSlippage()}

                <Card
                    sx={{ mb: -1, mt: 2, backgroundColor: 'transparent', boxShadow: 0 }}>
                    <ConnectWallet secondary='true' />
                    {addTokenToWallet()}

                    {swapButton()}
                </Card>
            </Box>
        </div>
    )
}

export default Swap
