import { useEffect } from 'react'
import { RecoilRoot } from 'recoil'
import RecoilNexus from 'recoil-nexus'
import { AppBar, Toolbar, Container, Grid, Box } from '@mui/material'
import { HashRouter as Router, Route, Switch } from 'react-router-dom'

import { updatePoolsAndTokens } from './services/amm'
import { updatePoolsAndTokensStatic } from './services/static_data'

import Main from './components/Main'
// import Wiper from './components/Wiper'
import Toasts from './components/Toasts'
import Charts from './components/Charts'
import Social from './components/Social'
// import AppMenu from './components/AppMenu'
// import SnTxSpeed from './components/SnTxSpeed'
import ConnectWallet from './components/ConnectWallet'
import PendingTransactions from './components/PendingTransactions'

import { GAinit, GApage, GAtrack } from './services/ga.js'

// import BgImage from './images/bgd/bg2.svg'
import BgImage from './images/bgd/bg2.png'
import LogoImage from './images/logo/Logo-mySwap-White.png'
import MobileLogoImage from './images/logo/mySwap_logo_mobile.svg'
import useIsMobile from './hooks/useIsMobile'

// UI Fixme: could not make the bgd image transparency 0.7 without effecting all other elements on top of it
const styles = {
    paperContainer: {
        backgroundImage: `url(${BgImage})`,
        backgroundSize: 'cover',
        backgroundPosition: 'top right',
        backgroundRepeat: 'no-repeat',
        width: '100%',
        height: '100%',
        minHeight: '100vh'
    }
}

function App() {
    useEffect(() => {
        GAinit()
        GApage('/')
        GAtrack('screen_res', window.innerWidth, window.innerHeight)
    }, [])

    // run once, on page load
    useEffect(() => {
        const f = async () => {
            await updatePoolsAndTokensStatic()
            updatePoolsAndTokens(false)
        }
        f()
    }, [])

    const onWalletConnected = async () => {
        console.log('Wallet connected')
    }
    const onWalletDisconnected = async () => {
        console.log('Wallet disconnected')
    }

    const isMobile = useIsMobile()
    const logo = isMobile ? MobileLogoImage : LogoImage

    return (
        <Router>
            <RecoilRoot>
                <RecoilNexus />
                <div className='App'>
                    <Grid className='op2' style={styles.paperContainer}>
                        <Grid item>
                            <AppBar class='Rectangle-1'>
                                <Toolbar
                                    sx={{
                                        display: 'flex',
                                        flexDirection: 'row',
                                        justifyContent: 'space-between',
                                        px: 2
                                    }}>
                                    <Box sx={{ display: 'flex' }}>
                                        <img
                                            src={logo}
                                            alt=''
                                            width={isMobile ? 24 : 150}
                                            height={isMobile ? 24 : 40}
                                        />
                                        {!isMobile && <Social />}
                                    </Box>
                                    <Box sx={{ justifySelf: 'end', display: 'flex' }}>
                                        <PendingTransactions />
                                        <ConnectWallet
                                            connected={onWalletConnected}
                                            disconnected={onWalletDisconnected}
                                        />
                                    </Box>
                                </Toolbar>
                            </AppBar>
                        </Grid>

                        <Grid item>
                            <Switch>
                                <Route exact path='/'>
                                    <Container sx={{ px: isMobile ? 1 : 2 }}>
                                        <Main />
                                        {/* <SnTxSpeed /> */}
                                    </Container>
                                </Route>
                                <Route path='/charts'>
                                    <Charts />
                                </Route>
                                {/* <Route path='/wipe'>
                                    <Container maxWidth='sm'>
                                        <Wiper />
                                    </Container>
                                </Route> */}
                            </Switch>
                        </Grid>
                        <Toasts />
                        {/* <Grid
                            item
                            alignItems='flex-end'
                            sx={{ position: 'fixed', bottom: 0, mb: 2 }}>
                            <Social />
                        </Grid> */}
                    </Grid>
                </div>
            </RecoilRoot>
        </Router>
    )
}

export default App
