import React from 'react'

import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'
import { IconButton, AppBar, Modal, Grid, Divider } from '@mui/material'
import { styled } from '@mui/material/styles'
import { StyledLoadingButton } from './StyleCustomizations'
import ContentCopyIcon from '@mui/icons-material/ContentCopy'
import useIsMobile from '../hooks/useIsMobile'

const Backdrop = styled('div')`
    z-index: -1;
    position: fixed;
    right: 0;
    bottom: 0;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.8);
    -webkit-tap-highlight-color: transparent;
`

function ConnectedWalletOptions(props) {
    const isMobile = useIsMobile()

    const handleClose = () => {
        props.setOpenConnectedOptions(false)
    }

    const openVoyager = () => {
        window.open(props.voyagerUrl, '_blank')
        props.setOpenConnectedOptions(false)
    }
    const disconnect = () => {
        props.disconnect()
        handleClose()
    }

    return (
        <div>
            <Modal open={true} onClose={handleClose} BackdropComponent={Backdrop}>
                <Box
                    sx={{
                        position: 'absolute',
                        top: '50%',
                        left: '50%',
                        transform: 'translate(-50%, -50%)',
                        width: isMobile ? 'calc(100% - 24px)' : '30%',
                        minWidth: !isMobile ? '576px' : 'none',
                        backgroundColor: 'rgba(13, 12, 31, 1)',
                        border: '1px solid rgba(255, 255, 255, 0.1)',
                        boxShadow: 40,
                        borderRadius: isMobile ? 0.75 : 2,
                        py: 3,
                        px: isMobile ? 2 : 3
                    }}>
                    <AppBar
                        position='static'
                        sx={{
                            boxShadow: 40,
                            borderRadius: 0.7,
                            backgroundColor: '#3649AC'
                        }}>
                        <Typography
                            fontSize={isMobile ? 16 : 24}
                            fontWeight='bold'
                            sx={{ textAlign: 'center', mt: 1, mb: 1 }}>
                            Connected with {props.walletName}
                        </Typography>
                    </AppBar>

                    <Divider
                        variant='middle'
                        sx={{
                            borderBottomWidth: 1,
                            backgroundColor: 'rgba(255, 255, 255, 0.2)'
                        }}
                    />
                    <Box py={2}>
                        <Grid container spacing={isMobile ? 1 : 2}>
                            <Grid item xs={2} sm={1} sx={{ mt: 0.8 }}>
                                <img
                                    src={props.walletIcon}
                                    alt=''
                                    width='24'
                                    height='24'
                                />
                            </Grid>
                            <Grid item xs={8} sm={10}>
                                <Typography
                                    fontSize={isMobile ? 16 : 24}
                                    marginTop={isMobile ? '10px' : 0}>
                                    {props.walletAddress.substr(0, 12) +
                                        '...' +
                                        props.walletAddress.substr(-10)}
                                </Typography>
                            </Grid>
                            <Grid
                                item
                                xs={2}
                                sm={1}
                                justifyContent='left'
                                sx={{ pl: isMobile ? 0 : 1 }}>
                                <IconButton
                                    onClick={() => {
                                        navigator.clipboard.writeText(props.walletAddress)
                                    }}>
                                    <ContentCopyIcon style={{ color: 'white' }} />
                                </IconButton>
                            </Grid>
                        </Grid>
                    </Box>
                    <Grid container justifyContent='space-between'>
                        <StyledLoadingButton
                            sx={{
                                width: isMobile ? '45%' : '30%',
                                ml: isMobile ? 0 : 2,
                                fontSize: isMobile ? 12 : 14,
                                borderRadius: 0.7
                            }}
                            onClick={disconnect}>
                            DISCONNECT
                        </StyledLoadingButton>
                        <StyledLoadingButton
                            sx={{
                                width: isMobile ? '45%' : '30%',
                                fontSize: isMobile ? 12 : 14,
                                borderRadius: 0.7
                            }}
                            onClick={openVoyager}>
                            View on Voyager
                        </StyledLoadingButton>
                    </Grid>
                </Box>
            </Modal>
        </div>
    )
}

export default ConnectedWalletOptions
