import React, { useState, useEffect } from 'react'

import { walletAddressState, tokensState } from '../atoms'
import { setRecoil } from 'recoil-nexus'
import { useRecoilState } from 'recoil'

import {
    updateAllTokensDynamicData,
    humanReadableHash,
    addToastMsg,
    updateUserLPBalance
} from '../services/amm'
import {
    addWalletChangeListener,
    connectWallet,
    connectWalletWithPopup,
    disconnectWallet,
    getExplorerUrlBase,
    isDappApproved,
    networkId,
    removeWalletChangeListener,
    walletAddress
} from '../services/wallet.service'
import { StyledLoadingButton, StyledButtonTypography } from './StyleCustomizations'

import { GAbutton, GAtrack } from '../services/ga.js'

import { Button, Typography, Grid, Select, MenuItem, FormControl } from '@mui/material'
import { getStarknet } from 'get-starknet-wallet'

import ArgentLogo from '../images/logo/Argent-x-logo.png'
import StarknetLogo from '../images/logo/StarkNet Icon.png'
import EthLogo from '../images/logo/eth@2x.png'
import ConnectedWalletOptions from './ConnectedWalletOptions'
import useIsMobile from '../hooks/useIsMobile'
// import SocialMenu from './SocialMenu'

function ConnectWallet(props) {
    const [walletConnectionStatus, setWalletConnectionStatus] = useState(0)
    const [networkName, setNetworkName] = useState('goerli-alpha')
    const [changeListenerRegistered, setChangeListenerRegistered] = useState(false)
    const [address, setAddress] = useRecoilState(walletAddressState)
    const [tokens] = useRecoilState(tokensState)
    const [openConnectedOptions, setOpenConnectedOptions] = useState(false)
    const [autoConnect, setAutoConnect] = useState(true)
    const [anchorEl, setAnchorEl] = useState()

    const isMobile = useIsMobile()

    const handleClick = async () => {
        GAbutton('connect_wallet')

        console.log('handleClick')
        setWalletConnectionStatus(1)
        try {
            const addr = autoConnect
                ? await connectWallet()
                : await connectWalletWithPopup()
            if (addr.length === 0) throw new Error('No wallet address found')
        } catch {
            // this means no argent wallet is available
            console.log('connectWallet throw an exception')
            //addToastMsg('Wallet not found', 'error')
            setWalletConnectionStatus(0)
            return
        }
        let a = await walletAddress()
        setAddress(a)
        setWalletConnectionStatus(2)
        if (!props.secondary && !changeListenerRegistered) {
            console.log('addWalletChangeListener')
            addWalletChangeListener(handleWalletChange)
            setChangeListenerRegistered(true) // do this only once, otherwise for every change a new listener will be registered
            if (props.connected) props.connected()
        }
        addToastMsg('Wallet connected', 'success')
        GAtrack('wallet', 'connect', a)
        const name = getStarknet()?.name ?? 'noname'
        console.log('wallet name: ', name)
        GAtrack('wallet', 'name', name)

        // GAbutton('wallet='+a)
    }

    useEffect(() => {
        if (props.secondary) return

        const handlePreApprove = async () => {
            if (await isDappApproved()) {
                console.log('dapp already approved')
                if (walletConnectionStatus === 0) handleClick()
            }
        }

        handlePreApprove()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const handleWalletChange = async (accounts) => {
        if (props.secondary) return
        console.log('handleWalletChange: ', accounts)
        let localWalletAddress = await walletAddress()
        setAddress(localWalletAddress)
        setRecoil(walletAddressState, localWalletAddress)
        setNetworkName(networkId())

        updateAllTokensDynamicData(true)
        updateUserLPBalance(true)
        addToastMsg('Wallet network/account change', 'success')
        if (props.connected) props.connected()
    }

    useEffect(() => {
        if (props.secondary) return
        if (!autoConnect) return

        console.log('address has changed, ', address)
        if (address && tokens) {
            setWalletConnectionStatus(2)
            updateAllTokensDynamicData()
            setNetworkName(networkId())
        }
    }, [
        address,
        tokens,
        props.secondary,
        autoConnect,
        changeListenerRegistered,
        setAddress
    ])

    // SECONDARY
    // Handle wallet connect button inside the screen (aka secondary)
    if (props.secondary) {
        // If already connected no need to display button
        if (address !== '' || walletConnectionStatus === 2) {
            return <div></div>
        }

        // Secondary connect button - short-circuit all hooks
        let buttonText = 'CONNECT WALLET'
        let buttonDisabled = false
        if (walletConnectionStatus === 1) {
            buttonText = 'CONNECTING...'
            buttonDisabled = true
        }

        // Display button
        return (
            <div>
                <StyledLoadingButton
                    fullWidth={true}
                    disabled={buttonDisabled}
                    onClick={handleClick}>
                    {/* <AccountBalanceWalletIcon fontSize='medium' sx={{ mr: 1 }} /> */}
                    <StyledButtonTypography sx={{ fontSize: isMobile ? 12 : 20 }}>
                        {buttonText}
                    </StyledButtonTypography>
                </StyledLoadingButton>
            </div>
        )
    }

    const handleConnectedClick = async (event) => {
        // open popup
        console.log('Connected Click')
        setAnchorEl(event.currentTarget)
        setOpenConnectedOptions(true)
    }

    const disconnect = async () => {
        setAutoConnect(false)
        removeWalletChangeListener(handleWalletChange)
        disconnectWallet()
        setChangeListenerRegistered(false)
        setWalletConnectionStatus(0)
        setAddress(undefined)
        if (props.disconnected) props.disconnected()
    }

    const walletStatus = () => {
        if (walletConnectionStatus === 2) {
            //console.log('Wallet is connected!', address)
            const icon = getStarknet()?.icon
            return (
                <Button
                    sx={{
                        width: isMobile ? 'auto' : '160px',
                        height: '45px',
                        display: 'flex',
                        flexDirection: 'row',
                        justifyContent: 'space-between',
                        alignItems: 'center',
                        borderRadius: '10px',
                        backgroundColor: '#302e54'
                    }}
                    onClick={handleConnectedClick}>
                    <img
                        src={icon ?? ArgentLogo}
                        alt=''
                        width={isMobile ? 20 : 24}
                        height={isMobile ? 20 : 24}
                    />
                    <Typography
                        sx={{
                            fontSize: isMobile ? 14 : 18,
                            pl: isMobile ? 1 : 0,
                            color: 'rgba(240, 240, 240, 0.7)'
                        }}>
                        {humanReadableHash(address, isMobile)}
                    </Typography>
                </Button>
            )
        } else if (walletConnectionStatus === 1) {
            //console.log('Wallet is being connected!', address)
            return (
                <Button
                    sx={{
                        width: isMobile ? 'auto' : '200px',
                        height: isMobile ? '45px' : '100%',
                        display: 'flex',
                        flexDirection: 'row',
                        justifyContent: 'center',
                        alignItems: 'center',
                        borderRadius: '10px',
                        backgroundColor: '#302e54 !important'
                    }}>
                    <Typography sx={{ fontSize: isMobile ? 14 : 18 }}>
                        Connecting...
                    </Typography>
                </Button>
            )
        } else {
            return (
                <Button
                    sx={{
                        width: isMobile ? 'auto' : '200px',
                        height: isMobile ? '45px' : '100%',
                        display: 'flex',
                        flexDirection: 'row',
                        justifyContent: 'center',
                        alignItems: 'center',
                        borderRadius: '10px',
                        backgroundColor: '#302e54'
                    }}
                    onClick={handleClick}>
                    <Typography
                        sx={{
                            lineHeight: 'unset',
                            height: 'unset',
                            fontSize: isMobile ? 14 : 18
                        }}>
                        Connect Wallet
                    </Typography>
                </Button>
            )
        }
    }

    // Main return
    return (
        <div>
            <Grid
                container
                direction='row'
                spacing={isMobile ? 1 : 2}
                alignItems='center'>
                <Grid item sx={{ pl: isMobile ? 0 : 1 }}>
                    <FormControl
                        fullWidth
                        sx={{
                            pr: '2px',
                            pl: '8px',
                            borderRadius: '10px',
                            backgroundColor: '#302e54',
                            maxWidth: isMobile ? '62px' : 'none'
                        }}>
                        <Select
                            class='Connect-Wallet'
                            variant='standard'
                            disableUnderline={true}
                            value={networkName}
                            //onChange={handleChange}
                            sx={{
                                fontSize: isMobile ? 14 : 18,
                                color: 'rgba(240, 240, 240, 0.7)',
                                mt: 0,
                                '.MuiSelect-icon': {
                                    fill: 'rgba(240, 240, 240, 0.7)'
                                },
                                '& .MuiTypography-root': {
                                    display: isMobile ? 'none' : 'block'
                                }
                            }}
                            MenuProps={{
                                sx: {
                                    mt: 1,
                                    '&& .MuiMenu-list': {
                                        backgroundColor: '#302e54',
                                        marginLeft: -2
                                    }
                                }
                            }}>
                            <MenuItem value={'goerli-alpha'}>
                                {/* <CircleIcon
                                        sx={{ mr: 1, ml: 1 }}
                                        style={{ fill: 'lightgreen', fontSize: 10 }}
                                    /> */}
                                <Grid
                                    container
                                    direction='row'
                                    justifyContent='center'
                                    alignItems='center'>
                                    <Grid item sx={{ mr: 1, mt: 1 }}>
                                        <img
                                            src={StarknetLogo}
                                            alt=''
                                            width='24'
                                            height='24'
                                        />
                                    </Grid>

                                    <Grid item sx={{ mr: 1, textAlign: 'center' }}>
                                        <Typography
                                            fontSize={18}
                                            sx={{
                                                color: 'rgba(240, 240, 240, 0.7)'
                                            }}>
                                            Starknet Goerli
                                        </Typography>
                                    </Grid>
                                </Grid>
                            </MenuItem>
                            {/*fix onClick*/}
                            <MenuItem
                                class='Frame-2'
                                disabled={true}
                                // onMouseEnter={(e) => e.target.style.backgroundColor = '#201e43'}
                                value={'mainnet-alpha'}>
                                <Grid
                                    container
                                    direction='row'
                                    justifyContent='center'
                                    alignItems='center'>
                                    <Grid item sx={{ mr: 1, ml: 1, mt: 1 }}>
                                        <img
                                            src={EthLogo}
                                            alt=''
                                            width='24'
                                            height='24'
                                        />
                                    </Grid>

                                    <Grid item sx={{ mr: 1 }}>
                                        {/* UI Fixme: change the color once Mainnet is enabled */}
                                        <Typography
                                            fontSize={18}
                                            sx={{
                                                color: 'rgba(240, 240, 240, 0.3)'
                                            }}>
                                            Starknet Mainnet
                                        </Typography>
                                    </Grid>
                                </Grid>
                            </MenuItem>
                        </Select>
                    </FormControl>
                </Grid>

                <Grid item>
                    {/* Add Wallet Status button */}
                    {walletStatus()}
                </Grid>
                {/* {isMobile && (
                    <Grid item sx={{ mt: 1.25 }}>
                        <SocialMenu />
                    </Grid>
                )} */}
            </Grid>

            {openConnectedOptions && (
                <ConnectedWalletOptions
                    setOpenConnectedOptions={setOpenConnectedOptions}
                    walletName={getStarknet()?.name}
                    walletAddress={address}
                    walletIcon={getStarknet()?.icon ?? ArgentLogo}
                    voyagerUrl={getExplorerUrlBase() + '/contract/' + address}
                    disconnect={disconnect}
                    anchorEl={anchorEl}
                />
            )}
        </div>
    )
}

export default ConnectWallet
